import React, { useState, useEffect, useCallback } from 'react';
import { Plus, Edit3, Trash2, RotateCcw, Copy, Clock, AlertTriangle } from 'lucide-react';
import axios from 'axios';
import toast from 'react-hot-toast';
import HyperPlanningView from '../components/HyperPlanningView';
import PlanningModal from '../components/PlanningModal';
import AdvancedPlanningTools from '../components/AdvancedPlanningTools';
import PlanningAnalytics from '../components/PlanningAnalytics';

const Planning = () => {
  const [slots, setSlots] = useState([]);
  const [students, setStudents] = useState([]);
  const [projects, setProjects] = useState([]);
  const [stats, setStats] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [editingSlot, setEditingSlot] = useState(null);
  // eslint-disable-next-line no-unused-vars
  const [initialSlotData, setInitialSlotData] = useState(null);
  const [selectedPhase, setSelectedPhase] = useState('all');
  const [viewMode, setViewMode] = useState('hyperplanning'); // 'hyperplanning', 'table'
  const [copiedSlot, setCopiedSlot] = useState(null);
  
  // Nouvelles fonctionnalits avances
  const [selectedSlots, setSelectedSlots] = useState([]); // Slection multiple
  const [history, setHistory] = useState([]); // Historique des modifications
  const [showHistory, setShowHistory] = useState(false);
  const [conflictSlots, setConflictSlots] = useState([]); // Crneaux en conflit
  const [isMultiSelect, setIsMultiSelect] = useState(false); // Mode slection multiple
  const [lastAction, setLastAction] = useState(null); // Dernire action pour Undo
  
  // tat pour prserver la vue actuelle lors des modifications
  const [currentViewState, setCurrentViewState] = useState({
    selectedWeek: null,
    viewMode: 'hyperplanning'
  });



  useEffect(() => {
    fetchData();
  }, []);

  // Gestion de l'historique
  const addToHistory = useCallback((action, data) => {
    const historyEntry = {
      id: Date.now(),
      action,
      data,
      timestamp: new Date().toISOString(),
      description: getActionDescription(action, data)
    };
    
    setHistory(prev => [historyEntry, ...prev.slice(0, 49)]); // Garder 50 dernires actions
    setLastAction(historyEntry);
  }, []);

  const getActionDescription = (action, data) => {
    switch (action) {
      case 'create': return `Cration du crneau ${data.first_name} ${data.last_name}`;
      case 'update': return `Modification du crneau ${data.first_name} ${data.last_name}`;
      case 'delete': return `Suppression du crneau ${data.first_name} ${data.last_name}`;
      case 'move': return `Dplacement du crneau ${data.first_name} ${data.last_name}`;
      case 'resize': return `Redimensionnement du crneau ${data.first_name} ${data.last_name}`;
      case 'delete_all': return `Suppression de tous les crneaux (${data.slots.length} crneau${data.slots.length > 1 ? 'x' : ''})`;
      default: return 'Action inconnue';
    }
  };

  const handleUndo = async () => {
    if (!lastAction) {
      toast.error('Aucune action  annuler');
      return;
    }

    try {
      switch (lastAction.action) {
        case 'create':
          await handleDeleteSlot(lastAction.data.id);
          break;
        case 'delete':
          await handleSlotSave(lastAction.data);
          break;
        case 'update':
        case 'move':
        case 'resize':
          await handleSlotSave(lastAction.data);
          break;
        case 'delete_all':
          // Restaurer tous les crneaux supprims
          for (const slot of lastAction.data.slots) {
            await handleSlotSave(slot);
          }
          break;
        default:
          toast.error('Type d\'action non support pour l\'annulation');
          break;
      }
      
      toast.success('Action annule');
      setLastAction(null);
    } catch (error) {
      toast.error('Impossible d\'annuler cette action');
    }
  };

  // Validation des conflits
  const checkConflicts = useCallback((slot, excludeId = null) => {
    const conflicts = slots.filter(s => 
      s.id !== excludeId &&
      s.start_date === slot.start_date &&
      s.student_id === slot.student_id &&
      s.start_time && slot.start_time &&
      s.end_time && slot.end_time &&
      (
        (s.start_time < slot.end_time && s.end_time > slot.start_time) ||
        (slot.start_time < s.end_time && slot.end_time > s.start_time)
      )
    );
    
    setConflictSlots(conflicts);
    return conflicts.length > 0;
  }, [slots]);

  // Slection multiple
  const handleSlotSelect = (slot, isMultiSelect = false) => {
    if (!isMultiSelect) {
      setSelectedSlots([slot]);
    } else {
      setSelectedSlots(prev => {
        const isSelected = prev.find(s => s.id === slot.id);
        if (isSelected) {
          return prev.filter(s => s.id !== slot.id);
        } else {
          return [...prev, slot];
        }
      });
    }
  };

  const handleDeleteMultipleSlots = async () => {
    if (selectedSlots.length === 0) return;
    
    if (!window.confirm(`tes-vous sr de vouloir supprimer ${selectedSlots.length} crneau${selectedSlots.length > 1 ? 'x' : ''} ?`)) {
      return;
    }

    try {
      await Promise.all(selectedSlots.map(slot => handleDeleteSlot(slot.id)));
      setSelectedSlots([]);
      toast.success(`${selectedSlots.length} crneau${selectedSlots.length > 1 ? 'x' : ''} supprim${selectedSlots.length > 1 ? 's' : ''}`);
    } catch (error) {
      toast.error('Erreur lors de la suppression multiple');
    }
  };

  // Fonction pour supprimer tous les crneaux
  const handleDeleteAllSlots = async () => {
    const confirmed = window.confirm('tes-vous sr de vouloir supprimer TOUS les crneaux ? Cette action est irrversible.');
    if (!confirmed) return;

    try {
      setLoading(true);
      
      // Ajouter  l'historique avant suppression
      addToHistory('delete_all', { slots: [...slots] });
      
      await axios.delete('/api/planning/');
      
      setSlots([]);
      setSelectedSlots([]);
      setIsMultiSelect(false);
      
      toast.success('Tous les crneaux ont t supprims avec succs');
    } catch (error) {
      console.error('Erreur lors de la suppression de tous les crneaux:', error);
      toast.error('Erreur lors de la suppression de tous les crneaux');
    } finally {
      setLoading(false);
    }
  };

  const fetchData = async (preserveView = false) => {
    try {
      setLoading(true);
      const [slotsRes, studentsRes, projectsRes, statsRes] = await Promise.all([
        axios.get('/api/planning'),
        axios.get('/api/students'),
        axios.get('/api/projects'),
        axios.get('/api/planning/stats')
      ]);

      if (slotsRes.data.success) {
        // viter les logs rpts en vrifiant si les donnes ont chang
        const newSlots = slotsRes.data.data;
        if (JSON.stringify(newSlots) !== JSON.stringify(slots)) {
          // console.log(' Crneaux reus:', newSlots);
          // Nettoyer et valider les donnes des crneaux
          const cleanedSlots = newSlots.filter(slot => {
          // Vrifier que le slot a toutes les proprits ncessaires
          return slot && 
                 slot.id && 
                 slot.start_date && 
                 slot.end_date && 
                 slot.first_name && 
                 slot.last_name &&
                 slot.project_title;
        }).map(slot => {
          // Normaliser le format de date
          const normalizeDate = (dateStr) => {
            if (!dateStr) return new Date().toISOString().split('T')[0];
            
            // Si c'est dj au format YYYY-MM-DD
            if (/^\d{4}-\d{2}-\d{2}$/.test(dateStr)) {
              return dateStr;
            }
            
            // Si c'est au format DD/MM/YYYY
            if (/^\d{2}\/\d{2}\/\d{4}$/.test(dateStr)) {
              const [day, month, year] = dateStr.split('/');
              return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`;
            }
            
            // Si c'est une date ISO
            if (dateStr.includes('T')) {
              return dateStr.split('T')[0];
            }
            
            // Par dfaut
            return new Date().toISOString().split('T')[0];
          };
          
          return {
            ...slot,
            // Normaliser les dates
            start_date: normalizeDate(slot.start_date),
            end_date: normalizeDate(slot.end_date),
            start_time: slot.start_time || '08:00',
            end_time: slot.end_time || '12:00',
            first_name: slot.first_name || 'tudiant',
            last_name: slot.last_name || 'Inconnu',
            project_title: slot.project_title || 'Projet',
            phase: slot.phase || 1,
            location: slot.location || 'Atelier',
            notes: slot.notes || '',
            status: slot.status || 'scheduled',
            color: slot.color || '#3B82F6'
          };
          });
          
          // console.log(' Crneaux nettoys:', cleanedSlots);
          setSlots(cleanedSlots);
        }
      }
      
      if (studentsRes.data.success) {
        setStudents(studentsRes.data.data);
      }
      
      if (projectsRes.data.success) {
        setProjects(projectsRes.data.data);
      }
      
      if (statsRes.data.success) {
        setStats(statsRes.data.data);
      }
    } catch (error) {
      console.error('Erreur lors du chargement des donnes:', error);
      toast.error('Erreur lors du chargement des donnes');
    } finally {
      setLoading(false);
    }
  };

  const handleCreateSlot = (initialSlotData = null) => {
    // Pour la cration, on ne set pas editingSlot mais on passe les donnes initiales au modal
    setEditingSlot(null);
    setInitialSlotData(initialSlotData);
    setShowModal(true);
  };

  const handleEditSlot = (slot) => {
    setEditingSlot(slot);
    setInitialSlotData(null); // On rinitialise les donnes initiales pour l'dition
    setShowModal(true);
  };

  const handleDeleteSlot = async (slotId) => {
    if (!window.confirm('tes-vous sr de vouloir supprimer ce crneau ?')) {
      return;
    }

    try {
      // Sauvegarder l'tat avant suppression pour l'historique
      const slotToDelete = slots.find(s => s.id === slotId);
      if (slotToDelete) {
        addToHistory('delete', slotToDelete);
      }

      const response = await axios.delete(`/api/planning/${slotId}`);
      if (response.data.success) {
        toast.success('Crneau supprim avec succs');
        fetchData();
      }
    } catch (error) {
      console.error('Erreur lors de la suppression:', error);
      toast.error('Erreur lors de la suppression du crneau');
    }
  };

  const handleSlotSave = async (slotData) => {
    try {
      let response;
      if (slotData.id) {
        // Mise  jour d'un crneau existant
        response = await axios.put(`/api/planning/${slotData.id}`, slotData);
        if (response.data.success) {
          // Invalider le cache du crneau modifi
          const { planningEngine } = await import('../components/PlanningEngine');
          planningEngine.invalidateSlotCache(slotData.id);
          
          toast.success('Crneau mis  jour avec succs');
        }
      } else {
        // Cration d'un nouveau crneau
        response = await axios.post('/api/planning', slotData);
        if (response.data.success) {
          toast.success('Crneau cr avec succs');
        }
      }
      
      if (response.data.success) {
        fetchData(true); // Prserver la vue actuelle
      }
    } catch (error) {
      console.error('Erreur lors de la sauvegarde:', error);
      toast.error('Erreur lors de la sauvegarde du crneau');
    }
  };

  const handleSlotMove = async (slotId, newDate, newTime) => {
    try {
      const slot = slots.find(s => s.id === slotId);
      if (!slot) {
        toast.error('Crneau non trouv');
        return;
      }

      const updatedSlot = {
        ...slot,
        start_date: newDate,
        start_time: newTime,
        end_date: newDate
      };

      // Vrifier les conflits avant dplacement
      const hasConflicts = checkConflicts(updatedSlot, slotId);
      if (hasConflicts) {
        const confirmMove = window.confirm(
          ` Conflit dtect ! ${conflictSlots.length} crneau${conflictSlots.length > 1 ? 'x' : ''} en conflit.\n\nVoulez-vous continuer ?`
        );
        if (!confirmMove) return;
      }

      // Sauvegarder l'tat avant dplacement pour l'historique
      addToHistory('move', slot);

      const response = await axios.put(`/api/planning/${slotId}`, updatedSlot);
      if (response.data.success) {
        // Invalider le cache du crneau dplac
        const { planningEngine } = await import('../components/PlanningEngine');
        planningEngine.invalidateSlotCache(slotId);
        
        toast.success('Crneau dplac avec succs');
        fetchData(true); // Prserver la vue actuelle
      }
    } catch (error) {
      console.error('Erreur lors du dplacement:', error);
      if (error.response && error.response.data) {
        console.error('Dtails de l\'erreur:', error.response.data);
        toast.error(error.response.data.message || 'Erreur lors du dplacement du crneau');
      } else {
        toast.error('Erreur lors du dplacement du crneau');
      }
    }
  };

  // eslint-disable-next-line no-unused-vars
  const handleSlotResize = async (slot, newTimes) => {
    try {
      const updatedSlot = {
        ...slot,
        start_time: newTimes.start_time,
        end_time: newTimes.end_time
      };

      // Vrifier les conflits avant redimensionnement
      const hasConflicts = checkConflicts(updatedSlot, slot.id);
      if (hasConflicts) {
        const confirmResize = window.confirm(
          ` Conflit dtect ! ${conflictSlots.length} crneau${conflictSlots.length > 1 ? 'x' : ''} en conflit.\n\nVoulez-vous continuer ?`
        );
        if (!confirmResize) return;
      }

      // Sauvegarder l'tat avant redimensionnement pour l'historique
      addToHistory('resize', slot);

      const response = await axios.put(`/api/planning/${slot.id}`, updatedSlot);
      if (response.data.success) {
        // Invalider le cache du crneau redimensionn
        const { planningEngine } = await import('../components/PlanningEngine');
        planningEngine.invalidateSlotCache(slot.id);
        
        toast.success('Crneau redimensionn avec succs');
        fetchData(true); // Prserver la vue actuelle
      }
    } catch (error) {
      console.error('Erreur lors du redimensionnement:', error);
      toast.error('Erreur lors du redimensionnement du crneau');
    }
  };

  const handleCopySlot = (slot) => {
    if (slot === null) {
      setCopiedSlot(null);
      toast.success('Copie annule');
    } else {
      setCopiedSlot(slot);
      toast.success('Crneau copi ! Cliquez sur un emplacement libre pour le coller.');
    }
  };

  const handlePasteSlot = async (targetDate, targetTime) => {
    if (!copiedSlot) {
      toast.error('Aucun crneau copi');
      return;
    }

    try {
      // Crer un nouveau crneau bas sur le crneau copi
      const newSlotData = {
        ...copiedSlot,
        id: undefined, // S'assurer qu'un nouvel ID sera gnr
        start_date: targetDate,
        start_time: targetTime,
        // Calculer l'heure de fin en conservant la dure du crneau original
        end_time: calculateEndTime(targetTime, copiedSlot.start_time, copiedSlot.end_time)
      };

      console.log('Collage du crneau:', newSlotData);
      const response = await axios.post('/api/planning', newSlotData);
      
      if (response.data.success) {
        toast.success('Crneau coll avec succs');
        setCopiedSlot(null); // Vider le presse-papiers
        fetchData(true); // Prserver la vue actuelle
      }
    } catch (error) {
      console.error('Erreur lors du collage:', error);
      toast.error('Erreur lors du collage du crneau');
    }
  };

  const calculateEndTime = (newStartTime, originalStartTime, originalEndTime) => {
    // Calculer la dure du crneau original
    const startParts = originalStartTime.split(':').map(Number);
    const endParts = originalEndTime.split(':').map(Number);
    const startMinutes = startParts[0] * 60 + startParts[1];
    const endMinutes = endParts[0] * 60 + endParts[1];
    const durationMinutes = endMinutes - startMinutes;

    // Calculer la nouvelle heure de fin
    const newStartParts = newStartTime.split(':').map(Number);
    const newStartMinutes = newStartParts[0] * 60 + newStartParts[1];
    const newEndMinutes = newStartMinutes + durationMinutes;
    
    const newEndHours = Math.floor(newEndMinutes / 60);
    const newEndMinutesRemainder = newEndMinutes % 60;
    
    return `${newEndHours.toString().padStart(2, '0')}:${newEndMinutesRemainder.toString().padStart(2, '0')}`;
  };

  const getPhaseColor = (phase) => {
    const colors = {
      1: 'bg-blue-100 text-blue-800',
      2: 'bg-orange-100 text-orange-800',
      3: 'bg-green-100 text-green-800'
    };
    return colors[phase] || 'bg-gray-100 text-gray-800';
  };

  const getPhaseTitle = (phase) => {
    const titles = {
      1: 'Planification',
      2: 'Pilotage',
      3: 'Soutenance'
    };
    return titles[phase] || 'Phase inconnue';
  };

  const filteredSlots = selectedPhase === 'all' 
    ? slots 
    : slots.filter(slot => slot.phase.toString() === selectedPhase);

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* En-tte */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Planification</h1>
          <p className="mt-1 text-sm text-gray-500">
            Gestion des crneaux de planification pour les trois phases
          </p>
        </div>
        <button
          onClick={handleCreateSlot}
          className="btn btn-primary flex items-center"
        >
          <Plus className="h-4 w-4 mr-2" />
          Nouveau crneau
        </button>
      </div>

      {/* Statistiques des phases */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {[1, 2, 3].map(phase => {
          const phaseStat = stats.find(s => s.phase === phase) || {
            total_slots: 0,
            scheduled: 0,
            in_progress: 0,
            completed: 0
          };
          
          return (
            <div key={phase} className="card">
              <div className="flex items-center justify-between mb-4">
                <div className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getPhaseColor(phase)}`}>
                  Phase {phase}: {getPhaseTitle(phase)}
                </div>
                <span className="text-2xl font-bold text-gray-900">
                  {phaseStat.total_slots}
                </span>
              </div>
              
              <div className="space-y-2">
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500">Programms</span>
                  <span className="font-medium">{phaseStat.scheduled}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500">En cours</span>
                  <span className="font-medium">{phaseStat.in_progress}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500">Termins</span>
                  <span className="font-medium">{phaseStat.completed}</span>
                </div>
              </div>
            </div>
          );
        })}
      </div>

      {/* Outils avancs */}
      <AdvancedPlanningTools
        slots={slots}
        onSlotCreate={handleCreateSlot}
        onSlotDelete={handleDeleteSlot}
        students={students}
        projects={projects}
        onValidateSchedule={(issues) => {
          console.log('Problmes de validation:', issues);
          // Ici vous pouvez ajouter une logique pour afficher les problmes
        }}
      />

      {/* Analyses et statistiques */}
      <PlanningAnalytics
        slots={slots}
        students={students}
        projects={projects}
      />

      {/* Barre d'outils avance */}
      <div className="card">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center space-x-4">
            <h3 className="text-lg font-medium text-gray-900">Vue :</h3>
            <div className="flex bg-gray-200 rounded-lg p-1">
              <button
                onClick={() => setViewMode('hyperplanning')}
                className={`px-4 py-2 text-sm rounded-md transition-colors ${
                  viewMode === 'hyperplanning' ? 'bg-white shadow-sm text-gray-900' : 'text-gray-600 hover:bg-gray-300'
                }`}
              >
                Planning
              </button>
              <button
                onClick={() => setViewMode('table')}
                className={`px-4 py-2 text-sm rounded-md transition-colors ${
                  viewMode === 'table' ? 'bg-white shadow-sm text-gray-900' : 'text-gray-600 hover:bg-gray-300'
                }`}
              >
                Tableau
              </button>
            </div>
          </div>
          <div className="flex items-center space-x-4">
            <label className="text-sm font-medium text-gray-700">Phase :</label>
            <select
              value={selectedPhase}
              onChange={(e) => setSelectedPhase(e.target.value)}
              className="input w-48"
            >
              <option value="all">Toutes les phases</option>
              <option value="1">Phase 1 - Planification</option>
              <option value="2">Phase 2 - Pilotage</option>
              <option value="3">Phase 3 - Soutenance</option>
            </select>
          </div>
        </div>

        {/* Outils avancs */}
        <div className="border-t pt-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              {/* Mode slection multiple */}
              <button
                onClick={() => setIsMultiSelect(!isMultiSelect)}
                className={`px-3 py-1.5 text-sm rounded-md transition-colors ${
                  isMultiSelect ? 'bg-blue-100 text-blue-700 border border-blue-300' : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
                }`}
                title="Mode slection multiple (Shift + Clic)"
              >
                <Copy className="h-4 w-4 mr-1" />
                Slection multiple
              </button>

              {/* Actions sur la slection */}
              {selectedSlots.length > 0 && (
                <>
                  <button
                    onClick={handleDeleteMultipleSlots}
                    className="px-3 py-1.5 text-sm bg-red-100 text-red-700 rounded-md hover:bg-red-200 transition-colors"
                    title={`Supprimer ${selectedSlots.length} crneau${selectedSlots.length > 1 ? 'x' : ''}`}
                  >
                    <Trash2 className="h-4 w-4 mr-1" />
                    Supprimer ({selectedSlots.length})
                  </button>
                  <button
                    onClick={() => handleCopySlot(selectedSlots[0])}
                    className="px-3 py-1.5 text-sm bg-blue-100 text-blue-700 rounded-md hover:bg-blue-200 transition-colors"
                    title="Copier le premier crneau slectionn"
                  >
                    <Copy className="h-4 w-4 mr-1" />
                    Copier
                  </button>
                </>
              )}

              {/* Historique */}
              <button
                onClick={() => setShowHistory(!showHistory)}
                className="px-3 py-1.5 text-sm bg-purple-100 text-purple-700 rounded-md hover:bg-purple-200 transition-colors"
                title="Historique des modifications"
              >
                <Clock className="h-4 w-4 mr-1" />
                Historique
              </button>

              {/* Annuler */}
              <button
                onClick={handleUndo}
                disabled={!lastAction}
                className={`px-3 py-1.5 text-sm rounded-md transition-colors ${
                  lastAction ? 'bg-orange-100 text-orange-700 hover:bg-orange-200' : 'bg-gray-100 text-gray-400 cursor-not-allowed'
                }`}
                title="Annuler la dernire action (Ctrl+Z)"
              >
                <RotateCcw className="h-4 w-4 mr-1" />
                Annuler
              </button>

              {/* Supprimer tous les crneaux */}
              <button
                onClick={handleDeleteAllSlots}
                className="px-3 py-1.5 text-sm bg-red-600 text-white rounded-md hover:bg-red-700 transition-colors"
                title="Supprimer tous les crneaux"
              >
                <Trash2 className="h-4 w-4 mr-1" />
                Supprimer tout
              </button>
            </div>

            {/* Raccourcis clavier */}
            <div className="text-xs text-gray-500">
              <div className="flex items-center space-x-4">
                <span>Ctrl+N : Nouveau</span>
                <span>Ctrl+C : Copier</span>
                <span>Ctrl+V : Coller</span>
                <span>Ctrl+Z : Annuler</span>
                <span>Delete : Supprimer</span>
                <span>chap : Dslectionner</span>
              </div>
            </div>
          </div>

          {/* Conflits dtects */}
          {conflictSlots.length > 0 && (
            <div className="mt-3 p-3 bg-red-50 border border-red-200 rounded-md">
              <div className="flex items-center">
                <AlertTriangle className="h-4 w-4 text-red-600 mr-2" />
                <span className="text-sm font-medium text-red-800">
                  {conflictSlots.length} crneau{conflictSlots.length > 1 ? 'x' : ''} en conflit dtect{conflictSlots.length > 1 ? 's' : ''}
                </span>
              </div>
            </div>
          )}
        </div>

        {/* Historique des modifications */}
        {showHistory && (
          <div className="mt-4 border-t pt-4">
            <h4 className="text-sm font-medium text-gray-900 mb-3">Historique des modifications</h4>
            <div className="max-h-40 overflow-y-auto space-y-2">
              {history.length === 0 ? (
                <p className="text-sm text-gray-500">Aucune modification rcente</p>
              ) : (
                history.slice(0, 10).map((entry) => (
                  <div key={entry.id} className="flex items-center justify-between text-sm">
                    <span className="text-gray-700">{entry.description}</span>
                    <span className="text-gray-500">
                      {new Date(entry.timestamp).toLocaleTimeString()}
                    </span>
                  </div>
                ))
              )}
            </div>
          </div>
        )}
      </div>

      {/* Affichage conditionnel selon le mode de vue */}
      {viewMode === 'hyperplanning' && (
        <div className="card p-0 h-[600px]">
          <HyperPlanningView
            slots={filteredSlots}
            onSlotEdit={handleEditSlot}
            onSlotDelete={handleDeleteSlot}
            onSlotMove={handleSlotMove}
            onSlotCreate={handleCreateSlot}
            onSlotResize={handleSlotResize}
            onSlotCopy={handleCopySlot}
            onSlotPaste={handlePasteSlot}
            copiedSlot={copiedSlot}
            selectedSlots={selectedSlots}
            onSlotSelect={handleSlotSelect}
            isMultiSelect={isMultiSelect}
            conflictSlots={conflictSlots}
            currentViewState={currentViewState}
            onViewStateChange={setCurrentViewState}
          />
        </div>
      )}

      {viewMode === 'table' && filteredSlots.length > 0 && (
        <div className="card">
          <h3 className="text-lg font-medium text-gray-900 mb-4">
            Liste des crneaux ({filteredSlots.length})
          </h3>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Phase
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    tudiant
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Date
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Jour
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Heure
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Projet
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Lieu
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Statut
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredSlots.map(slot => {
                  const date = new Date(slot.start_date);
                  const dayNames = ['Dimanche', 'Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi'];
                  const monthNames = ['Jan', 'Fv', 'Mar', 'Avr', 'Mai', 'Juin', 'Juil', 'Aot', 'Sep', 'Oct', 'Nov', 'Dc'];
                  
                  return (
                    <tr key={slot.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getPhaseColor(slot.phase)}`}>
                          Phase {slot.phase}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="flex items-center">
                          <div>
                            <div className="text-sm font-medium text-gray-900">
                              {slot.first_name} {slot.last_name}
                            </div>
                            <div className="text-sm text-gray-500">
                              {slot.class}
                            </div>
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {date.getDate()}/{monthNames[date.getMonth()]}/{date.getFullYear()}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {dayNames[date.getDay()]}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {slot.start_time ? (
                            <div>
                              <div>{slot.start_time.substring(0, 5)}</div>
                              {slot.end_time && (
                                <div className="text-xs text-gray-500">
                                   {slot.end_time.substring(0, 5)}
                                </div>
                              )}
                            </div>
                          ) : (
                            <span className="text-gray-400 italic">Non dfinie</span>
                          )}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {slot.project_title || <span className="text-gray-400 italic">Aucun</span>}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">
                          {slot.location || <span className="text-gray-400 italic">Non dfini</span>}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                          slot.status === 'scheduled' ? 'bg-blue-100 text-blue-800' :
                          slot.status === 'in_progress' ? 'bg-yellow-100 text-yellow-800' :
                          slot.status === 'completed' ? 'bg-green-100 text-green-800' :
                          'bg-red-100 text-red-800'
                        }`}>
                          {slot.status === 'scheduled' ? 'Programm' :
                           slot.status === 'in_progress' ? 'En cours' :
                           slot.status === 'completed' ? 'Termin' :
                           'Annul'}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                        <div className="flex items-center space-x-2">
                          <button
                            onClick={() => handleEditSlot(slot)}
                            className="text-blue-600 hover:text-blue-900"
                            title="Modifier"
                          >
                            <Edit3 className="h-4 w-4" />
                          </button>
                          <button
                            onClick={() => handleDeleteSlot(slot.id)}
                            className="text-red-600 hover:text-red-900"
                            title="Supprimer"
                          >
                            <Trash2 className="h-4 w-4" />
                          </button>
                        </div>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Modal de cration/dition */}
      {showModal && (
        <PlanningModal
          slot={editingSlot || initialSlotData}
          students={students}
          projects={projects}
          onSave={handleSlotSave}
          onClose={() => {
            setShowModal(false);
            setEditingSlot(null);
            setInitialSlotData(null);
          }}
        />
      )}
    </div>
  );
};

export default Planning;