import React, { useState } from 'react';
import { FileText, Plus, Search, Filter } from 'lucide-react';
import TPSheetModal from '../components/TPSheetModal';

const FicheTP = () => {
  const [showTPSheetModal, setShowTPSheetModal] = useState(false);

  const handleCreateTPSheet = () => {
    console.log(' Bouton Crer une fiche TP cliqu');
    setShowTPSheetModal(true);
    console.log(' showTPSheetModal dfini  true');
  };

  const handleSaveTPSheet = (content) => {
    console.log(' Sauvegarde de la fiche TP:', content);
    // Ici vous pouvez ajouter la logique pour sauvegarder la fiche TP
    alert('Fiche TP sauvegarde avec succs !');
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* En-tte */}
        <div className="mb-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900 flex items-center">
                <FileText className="h-8 w-8 mr-3 text-blue-600" />
                Fiche TP
              </h1>
              <p className="mt-2 text-gray-600">
                Gestion des fiches de travaux pratiques
              </p>
            </div>
            <button 
              onClick={handleCreateTPSheet}
              className="bg-blue-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-blue-700 transition-colors flex items-center"
            >
              <Plus className="h-4 w-4 mr-2" />
              Nouvelle fiche TP
            </button>
          </div>
        </div>

        {/* Barre de recherche et filtres */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 mb-6">
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="flex-1">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
                <input
                  type="text"
                  placeholder="Rechercher une fiche TP..."
                  className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>
            </div>
            <div className="flex gap-2">
              <select className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent">
                <option value="">Toutes les classes</option>
                <option value="STS ELEC1">STS ELEC1</option>
                <option value="STS ELEC2">STS ELEC2</option>
              </select>
              <select className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent">
                <option value="">Tous les types</option>
                <option value="TP">TP</option>
                <option value="TD">TD</option>
                <option value="Projet">Projet</option>
              </select>
              <button className="px-4 py-2 border border-gray-300 rounded-md hover:bg-gray-50 transition-colors flex items-center">
                <Filter className="h-4 w-4 mr-2" />
                Filtres
              </button>
            </div>
          </div>
        </div>

        {/* Contenu principal */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200">
          <div className="p-6">
            <div className="text-center py-12">
              <FileText className="h-16 w-16 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                Aucune fiche TP trouve
              </h3>
              <p className="text-gray-500 mb-6">
                Commencez par crer votre premire fiche de travaux pratiques.
              </p>
              <button 
                onClick={handleCreateTPSheet}
                className="bg-blue-600 text-white px-6 py-3 rounded-md text-sm font-medium hover:bg-blue-700 transition-colors flex items-center mx-auto"
              >
                <Plus className="h-4 w-4 mr-2" />
                Crer une fiche TP
              </button>
            </div>
          </div>
        </div>

        {/* Instructions pour le dveloppement */}
        <div className="mt-8 bg-yellow-50 border border-yellow-200 rounded-lg p-6">
          <h3 className="text-lg font-medium text-yellow-800 mb-2">
             Instructions pour le dveloppement
          </h3>
          <p className="text-yellow-700 mb-4">
            Cette page est prte pour recevoir les fonctionnalits de gestion des fiches TP. 
            En attente des spcifications dtailles pour l'implmentation.
          </p>
          <div className="text-sm text-yellow-600">
            <p><strong>Fonctionnalits suggres :</strong></p>
            <ul className="list-disc list-inside mt-2 space-y-1">
              <li>Cration et dition de fiches TP</li>
              <li>Gestion des objectifs pdagogiques</li>
              <li>Liste du matriel ncessaire</li>
              <li>Procdures dtailles</li>
              <li>valuation et critres de russite</li>
              <li>Association aux classes et matires</li>
            </ul>
          </div>
        </div>

        {/* Modal Fiche TP */}
        <TPSheetModal
          isOpen={showTPSheetModal}
          onClose={() => setShowTPSheetModal(false)}
          onSave={handleSaveTPSheet}
        />
      </div>
    </div>
  );
};

export default FicheTP;
