import React, { useState, useEffect } from 'react';
import { Plus, Edit, Trash2, Users, User, Upload, CheckSquare, Square } from 'lucide-react';
import axios from 'axios';
import toast from 'react-hot-toast';
import CSVImport from '../components/CSVImport';
import { useSchoolYear } from '../contexts/SchoolYearContext';

const Students = () => {
  const { selectedYear } = useSchoolYear();
  const [students, setStudents] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [showCSVImport, setShowCSVImport] = useState(false);
  const [editingStudent, setEditingStudent] = useState(null);
  const [selectedStudents, setSelectedStudents] = useState([]);
  const [selectAll, setSelectAll] = useState(false);
  const [formData, setFormData] = useState({
    first_name: '',
    last_name: '',
    date_de_naissance: '',
    email: '',
    class: '',
    project_id: '',
    role: '',
    school_year: selectedYear
  });


  useEffect(() => {
    fetchData();
  }, [selectedYear]);

  useEffect(() => {
    setFormData(prev => ({ ...prev, school_year: selectedYear }));
  }, [selectedYear]);

  useEffect(() => {
    setSelectAll(selectedStudents.length === students.length && students.length > 0);
  }, [selectedStudents, students]);

  const fetchData = async () => {
    try {
      const [studentsRes, projectsRes] = await Promise.all([
        axios.get(`/api/students?school_year=${selectedYear}`),
        axios.get('/api/projects')
      ]);

      if (studentsRes.data.success) {
        setStudents(studentsRes.data.data);
      }
      if (projectsRes.data.success) {
        setProjects(projectsRes.data.data);
      }
    } catch (error) {
      console.error('Erreur lors du chargement des donnes:', error);
      toast.error('Erreur lors du chargement des donnes');
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    try {
      if (editingStudent) {
        await axios.put(`/api/students/${editingStudent.id}`, formData);
        toast.success('tudiant mis  jour avec succs');
      } else {
        await axios.post('/api/students', formData);
        toast.success('tudiant cr avec succs');
      }
      
      setShowModal(false);
      setEditingStudent(null);
      resetForm();
      fetchData();
    } catch (error) {
      console.error('Erreur lors de la sauvegarde:', error);
      toast.error(error.response?.data?.message || 'Erreur lors de la sauvegarde');
    }
  };



  const handleDelete = async (id) => {
    if (!window.confirm('tes-vous sr de vouloir supprimer cet tudiant ?')) {
      return;
    }

    try {
      await axios.delete(`/api/students/${id}`);
      toast.success('tudiant supprim avec succs');
      fetchData();
    } catch (error) {
      console.error('Erreur lors de la suppression:', error);
      toast.error('Erreur lors de la suppression');
    }
  };

  const handleEdit = (student) => {
    setEditingStudent(student);
    
    // Convertir la date au format ISO pour l'input HTML
    let formattedDate = '';
    if (student.date_de_naissance) {
      try {
        // Si la date est au format franais (dd/mm/yyyy), la convertir
        if (student.date_de_naissance.includes('/')) {
          const [day, month, year] = student.date_de_naissance.split('/');
          formattedDate = `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`;
        } else {
          // Si c'est dj au format ISO, l'utiliser directement
          formattedDate = student.date_de_naissance;
        }
      } catch (error) {
        console.error('Erreur de format de date:', error);
        formattedDate = '';
      }
    }
    
    setFormData({
      first_name: student.first_name,
      last_name: student.last_name,
      date_de_naissance: formattedDate,
      email: student.email || '',
      class: student.class || '',
      project_id: student.project_id || '',
      role: student.role || ''
    });
    setShowModal(true);
  };

  const resetForm = () => {
    setFormData({
      first_name: '',
      last_name: '',
      date_de_naissance: '',
      email: '',
      class: '',
      project_id: '',
      role: ''
    });
  };

  const handleCSVImport = async (students) => {
    try {
      const response = await axios.post('/api/students/bulk', { students });
      
      if (response.data.success) {
        const { summary } = response.data;
        toast.success(
          `Import termin: ${summary.success} succs, ${summary.error} erreurs, ${summary.skipped} ignors`
        );
        fetchData(); // Refresh student list after import
      }
    } catch (error) {
      console.error('Erreur lors de l\'import:', error);
      toast.error(error.response?.data?.message || 'Erreur lors de l\'import');
    }
  };

  const handleSelectStudent = (studentId) => {
    setSelectedStudents(prev => {
      if (prev.includes(studentId)) {
        return prev.filter(id => id !== studentId);
      } else {
        return [...prev, studentId];
      }
    });
  };

  const handleSelectAll = () => {
    if (selectAll) {
      setSelectedStudents([]);
      setSelectAll(false);
    } else {
      setSelectedStudents(students.map(student => student.id));
      setSelectAll(true);
    }
  };

  const handleBulkDelete = async () => {
    if (selectedStudents.length === 0) {
      toast.error('Aucun tudiant slectionn');
      return;
    }

    const confirmMessage = `tes-vous sr de vouloir supprimer ${selectedStudents.length} tudiant(s) ? Cette action est irrversible.`;
    if (!window.confirm(confirmMessage)) {
      return;
    }

    try {
      const response = await axios.delete('/api/students/bulk', {
        data: { student_ids: selectedStudents }
      });
      
      if (response.data.success) {
        toast.success(response.data.message);
        setSelectedStudents([]);
        setSelectAll(false);
        fetchData();
      }
    } catch (error) {
      console.error('Erreur lors de la suppression en masse:', error);
      toast.error(error.response?.data?.message || 'Erreur lors de la suppression en masse');
    }
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* En-tte */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">tudiants</h1>
          <p className="mt-1 text-sm text-gray-500">
            Gestion des tudiants et des groupes
          </p>
        </div>
                 <div className="flex space-x-3">
          {selectedStudents.length > 0 && (
            <button
              onClick={handleBulkDelete}
              className="btn btn-danger flex items-center"
            >
              <Trash2 className="h-4 w-4 mr-2" />
              Supprimer ({selectedStudents.length})
            </button>
          )}
          <button
            onClick={() => setShowCSVImport(true)}
            className="btn btn-secondary flex items-center"
          >
            <Upload className="h-4 w-4 mr-2" />
            Import CSV
          </button>
          <button
            onClick={() => setShowModal(true)}
            className="btn btn-primary flex items-center"
          >
            <Plus className="h-4 w-4 mr-2" />
            Nouvel tudiant
          </button>
        </div>
      </div>

             {/* Statistiques */}
       <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
         <div className="bg-white overflow-hidden shadow rounded-lg">
           <div className="p-5">
             <div className="flex items-center">
               <div className="flex-shrink-0">
                 <Users className="h-6 w-6 text-gray-400" />
               </div>
               <div className="ml-5 w-0 flex-1">
                 <dl>
                   <dt className="text-sm font-medium text-gray-500 truncate">
                     Total tudiants
                   </dt>
                   <dd className="text-lg font-medium text-gray-900">
                     {students.length}
                   </dd>
                 </dl>
               </div>
             </div>
           </div>
         </div>

         <div className="bg-white overflow-hidden shadow rounded-lg">
           <div className="p-5">
             <div className="flex items-center">
               <div className="flex-shrink-0">
                 <User className="h-6 w-6 text-gray-400" />
               </div>
               <div className="ml-5 w-0 flex-1">
                 <dl>
                   <dt className="text-sm font-medium text-gray-500 truncate">
                     Projets
                   </dt>
                   <dd className="text-lg font-medium text-gray-900">
                     {projects.length}
                   </dd>
                 </dl>
               </div>
             </div>
           </div>
         </div>
       </div>

      

      {/* Liste des tudiants */}
      <div className="bg-white shadow overflow-hidden sm:rounded-md">
        {students.length === 0 ? (
          <div className="text-center py-12">
            <Users className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-sm font-medium text-gray-900">Aucun tudiant</h3>
            <p className="mt-1 text-sm text-gray-500">
              Commencez par ajouter un tudiant.
            </p>
            <div className="mt-6">
              <button
                onClick={() => setShowModal(true)}
                className="btn btn-primary"
              >
                <Plus className="h-4 w-4 mr-2" />
                Ajouter un tudiant
              </button>
            </div>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider w-12">
                    <button
                      onClick={handleSelectAll}
                      className="flex items-center justify-center w-6 h-6 text-gray-400 hover:text-gray-600"
                    >
                      {selectAll ? (
                        <CheckSquare className="h-5 w-5 text-blue-600" />
                      ) : (
                        <Square className="h-5 w-5" />
                      )}
                    </button>
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    tudiant
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Classe
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Groupe
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Date de naissance
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Email
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {students.map((student) => (
                  <tr key={student.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <button
                        onClick={() => handleSelectStudent(student.id)}
                        className="flex items-center justify-center w-6 h-6 text-gray-400 hover:text-gray-600"
                      >
                        {selectedStudents.includes(student.id) ? (
                          <CheckSquare className="h-5 w-5 text-blue-600" />
                        ) : (
                          <Square className="h-5 w-5" />
                        )}
                      </button>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10">
                          <div className="h-10 w-10 rounded-full bg-primary-100 flex items-center justify-center">
                            <User className="h-6 w-6 text-primary-600" />
                          </div>
                        </div>
                        <div className="ml-4">
                          <div className="text-sm font-medium text-gray-900">
                            {student.first_name} {student.last_name}
                          </div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {student.class || 'Non dfinie'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {student.group_name || 'Non assign'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {student.date_de_naissance ? new Date(student.date_de_naissance).toLocaleDateString('fr-FR') : 'Non renseigne'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {student.email || 'Non renseign'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                      <div className="flex space-x-2">
                        <button
                          onClick={() => handleEdit(student)}
                          className="text-primary-600 hover:text-primary-900"
                        >
                          <Edit className="h-4 w-4" />
                        </button>
                        <button
                          onClick={() => handleDelete(student.id)}
                          className="text-red-600 hover:text-red-900"
                        >
                          <Trash2 className="h-4 w-4" />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>

      {/* Modal tudiant */}
      {showModal && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowModal(false)} />
            
            <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
              <form onSubmit={handleSubmit}>
                <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                  <div className="mb-4">
                    <h3 className="text-lg font-medium text-gray-900">
                      {editingStudent ? 'Modifier l\'tudiant' : 'Nouvel tudiant'}
                    </h3>
                  </div>
                  
                  <div className="space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="label">Prnom *</label>
                        <input
                          type="text"
                          required
                          className="input"
                          value={formData.first_name}
                          onChange={(e) => setFormData({...formData, first_name: e.target.value})}
                        />
                      </div>
                      
                      <div>
                        <label className="label">Nom *</label>
                        <input
                          type="text"
                          required
                          className="input"
                          value={formData.last_name}
                          onChange={(e) => setFormData({...formData, last_name: e.target.value})}
                        />
                      </div>
                    </div>
                    
                    <div>
                      <label className="label">Date de naissance</label>
                      <input
                        type="date"
                        className="input"
                        value={formData.date_de_naissance}
                        onChange={(e) => setFormData({...formData, date_de_naissance: e.target.value})}
                      />
                    </div>
                    
                    <div>
                      <label className="label">Email</label>
                      <input
                        type="email"
                        className="input"
                        placeholder="exemple@email.com"
                        value={formData.email}
                        onChange={(e) => setFormData({...formData, email: e.target.value})}
                      />
                    </div>
                    
                    <div>
                      <label className="label">Classe</label>
                      <input
                        type="text"
                        className="input"
                        placeholder="BTS lectrotechnique 2me anne"
                        value={formData.class}
                        onChange={(e) => setFormData({...formData, class: e.target.value})}
                      />
                    </div>
                    
                    
                    
                    <div>
                      <label className="label">Projet</label>
                      <select
                        className="input"
                        value={formData.project_id}
                        onChange={(e) => setFormData({...formData, project_id: e.target.value})}
                      >
                        <option value="">Slectionner un projet</option>
                        {projects.map((project) => (
                          <option key={project.id} value={project.id}>
                            {project.title}
                          </option>
                        ))}
                      </select>
                    </div>
                    
                    <div>
                      <label className="label">Rle dans le projet</label>
                      <select
                        className="input"
                        value={formData.role}
                        onChange={(e) => setFormData({...formData, role: e.target.value})}
                      >
                        <option value="">Slectionner un rle</option>
                        <option value="conducteur">Conducteur de projet</option>
                        <option value="executant">Excutant</option>
                        <option value="assistant">Assistant</option>
                        <option value="observateur">Observateur</option>
                      </select>
                    </div>
                  </div>
                </div>
                
                <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                  <button
                    type="submit"
                    className="btn btn-primary sm:ml-3 sm:w-auto"
                  >
                    {editingStudent ? 'Mettre  jour' : 'Crer'}
                  </button>
                  <button
                    type="button"
                    className="btn btn-secondary sm:mt-0 sm:w-auto"
                    onClick={() => {
                      setShowModal(false);
                      setEditingStudent(null);
                      resetForm();
                    }}
                  >
                    Annuler
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      

      {/* Modal d'import CSV */}
      {showCSVImport && (
        <CSVImport
          onImport={handleCSVImport}
          onClose={() => setShowCSVImport(false)}
        />
      )}
    </div>
  );
};

export default Students; 