import React, { useMemo } from 'react';
import { BarChart3, TrendingUp, Clock, Users, Calendar, Target } from 'lucide-react';

const PlanningAnalytics = ({ slots, students, projects }) => {
  
  const analytics = useMemo(() => {
    if (!slots || !students || !projects) return null;

    // Calculs de base
    const totalSlots = slots.length;
    const totalStudents = students.length;
    const totalProjects = projects.length;
    
    // Rpartition par phase
    const phaseDistribution = slots.reduce((acc, slot) => {
      acc[slot.phase] = (acc[slot.phase] || 0) + 1;
      return acc;
    }, {});

    // Rpartition par tudiant
    const studentDistribution = slots.reduce((acc, slot) => {
      const studentName = `${slot.first_name} ${slot.last_name}`;
      acc[studentName] = (acc[studentName] || 0) + 1;
      return acc;
    }, {});

    // Calcul des heures totales
    const totalHours = slots.reduce((total, slot) => {
      if (slot.start_time && slot.end_time) {
        const start = new Date(`2000-01-01T${slot.start_time}`);
        const end = new Date(`2000-01-01T${slot.end_time}`);
        const duration = (end - start) / (1000 * 60 * 60); // heures
        return total + duration;
      }
      return total;
    }, 0);

    // Rpartition des heures par tudiant
    const hoursPerStudent = slots.reduce((acc, slot) => {
      const studentName = `${slot.first_name} ${slot.last_name}`;
      if (slot.start_time && slot.end_time) {
        const start = new Date(`2000-01-01T${slot.start_time}`);
        const end = new Date(`2000-01-01T${slot.end_time}`);
        const duration = (end - start) / (1000 * 60 * 60);
        acc[studentName] = (acc[studentName] || 0) + duration;
      }
      return acc;
    }, {});

    // Crneaux par jour de la semaine
    const dayDistribution = slots.reduce((acc, slot) => {
      const date = new Date(slot.start_date);
      const dayName = ['Dimanche', 'Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi'][date.getDay()];
      acc[dayName] = (acc[dayName] || 0) + 1;
      return acc;
    }, {});

    // Optimisations suggres
    const suggestions = [];
    
    // Vrifier les tudiants avec trop de crneaux
    const maxSlotsPerStudent = Math.max(...Object.values(studentDistribution));
    const overloadedStudents = Object.entries(studentDistribution)
      .filter(([name, count]) => count > maxSlotsPerStudent * 0.8)
      .map(([name, count]) => ({ name, count }));

    if (overloadedStudents.length > 0) {
      suggestions.push({
        type: 'warning',
        message: `${overloadedStudents.length} tudiant${overloadedStudents.length > 1 ? 's' : ''} avec une charge leve`,
        details: overloadedStudents.map(s => `${s.name} (${s.count} crneaux)`).join(', ')
      });
    }

    // Vrifier les jours surchargs
    const maxSlotsPerDay = Math.max(...Object.values(dayDistribution));
    const overloadedDays = Object.entries(dayDistribution)
      .filter(([day, count]) => count > maxSlotsPerDay * 0.8)
      .map(([day, count]) => ({ day, count }));

    if (overloadedDays.length > 0) {
      suggestions.push({
        type: 'info',
        message: `${overloadedDays.length} jour${overloadedDays.length > 1 ? 's' : ''} avec beaucoup de crneaux`,
        details: overloadedDays.map(d => `${d.day} (${d.count} crneaux)`).join(', ')
      });
    }

    // Vrifier les tudiants sans crneaux
    const studentsWithSlots = new Set(slots.map(s => s.student_id));
    const studentsWithoutSlots = students.filter(s => !studentsWithSlots.has(s.id));
    
    if (studentsWithoutSlots.length > 0) {
      suggestions.push({
        type: 'error',
        message: `${studentsWithoutSlots.length} tudiant${studentsWithoutSlots.length > 1 ? 's' : ''} sans crneaux`,
        details: studentsWithoutSlots.map(s => `${s.first_name} ${s.last_name}`).join(', ')
      });
    }

    return {
      totalSlots,
      totalStudents,
      totalProjects,
      totalHours,
      phaseDistribution,
      studentDistribution,
      hoursPerStudent,
      dayDistribution,
      suggestions
    };
  }, [slots, students, projects]);

  if (!analytics) {
    return (
      <div className="card">
        <div className="flex items-center justify-center h-32">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Mtriques principales */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 rounded-lg">
              <Calendar className="h-6 w-6 text-blue-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600">Total crneaux</p>
              <p className="text-2xl font-bold text-gray-900">{analytics.totalSlots}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 rounded-lg">
              <Users className="h-6 w-6 text-green-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600">tudiants</p>
              <p className="text-2xl font-bold text-gray-900">{analytics.totalStudents}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-purple-100 rounded-lg">
              <Target className="h-6 w-6 text-purple-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600">Projets</p>
              <p className="text-2xl font-bold text-gray-900">{analytics.totalProjects}</p>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center">
            <div className="p-2 bg-orange-100 rounded-lg">
              <Clock className="h-6 w-6 text-orange-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600">Heures totales</p>
              <p className="text-2xl font-bold text-gray-900">{analytics.totalHours.toFixed(1)}h</p>
            </div>
          </div>
        </div>
      </div>

      {/* Graphiques et analyses */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Rpartition par phase */}
        <div className="card">
          <h3 className="text-lg font-medium text-gray-900 mb-4">Rpartition par phase</h3>
          <div className="space-y-3">
            {Object.entries(analytics.phaseDistribution).map(([phase, count]) => (
              <div key={phase} className="flex items-center justify-between">
                <span className="text-sm font-medium text-gray-700">
                  Phase {phase}
                </span>
                <div className="flex items-center space-x-2">
                  <div className="w-32 bg-gray-200 rounded-full h-2">
                    <div 
                      className="bg-blue-600 h-2 rounded-full" 
                      style={{ width: `${(count / analytics.totalSlots) * 100}%` }}
                    ></div>
                  </div>
                  <span className="text-sm text-gray-600 w-8">{count}</span>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Rpartition par jour */}
        <div className="card">
          <h3 className="text-lg font-medium text-gray-900 mb-4">Rpartition par jour</h3>
          <div className="space-y-3">
            {['Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi', 'Dimanche'].map(day => {
              const count = analytics.dayDistribution[day] || 0;
              return (
                <div key={day} className="flex items-center justify-between">
                  <span className="text-sm font-medium text-gray-700">{day}</span>
                  <div className="flex items-center space-x-2">
                    <div className="w-32 bg-gray-200 rounded-full h-2">
                      <div 
                        className="bg-green-600 h-2 rounded-full" 
                        style={{ width: `${(count / Math.max(...Object.values(analytics.dayDistribution))) * 100}%` }}
                      ></div>
                    </div>
                    <span className="text-sm text-gray-600 w-8">{count}</span>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>

      {/* Suggestions d'optimisation */}
      {analytics.suggestions.length > 0 && (
        <div className="card">
          <h3 className="text-lg font-medium text-gray-900 mb-4">Suggestions d'optimisation</h3>
          <div className="space-y-3">
            {analytics.suggestions.map((suggestion, index) => (
              <div 
                key={index} 
                className={`p-3 rounded-lg border-l-4 ${
                  suggestion.type === 'error' ? 'bg-red-50 border-red-400' :
                  suggestion.type === 'warning' ? 'bg-yellow-50 border-yellow-400' :
                  'bg-blue-50 border-blue-400'
                }`}
              >
                <div className="flex items-start">
                  <div className={`p-1 rounded-full ${
                    suggestion.type === 'error' ? 'bg-red-100' :
                    suggestion.type === 'warning' ? 'bg-yellow-100' :
                    'bg-blue-100'
                  }`}>
                    {suggestion.type === 'error' ? (
                      <Target className="h-4 w-4 text-red-600" />
                    ) : suggestion.type === 'warning' ? (
                      <TrendingUp className="h-4 w-4 text-yellow-600" />
                    ) : (
                      <BarChart3 className="h-4 w-4 text-blue-600" />
                    )}
                  </div>
                  <div className="ml-3">
                    <p className={`text-sm font-medium ${
                      suggestion.type === 'error' ? 'text-red-800' :
                      suggestion.type === 'warning' ? 'text-yellow-800' :
                      'text-blue-800'
                    }`}>
                      {suggestion.message}
                    </p>
                    {suggestion.details && (
                      <p className={`text-sm mt-1 ${
                        suggestion.type === 'error' ? 'text-red-700' :
                        suggestion.type === 'warning' ? 'text-yellow-700' :
                        'text-blue-700'
                      }`}>
                        {suggestion.details}
                      </p>
                    )}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Top tudiants par charge de travail */}
      <div className="card">
        <h3 className="text-lg font-medium text-gray-900 mb-4">Charge de travail par tudiant</h3>
        <div className="space-y-3">
          {Object.entries(analytics.hoursPerStudent)
            .sort(([,a], [,b]) => b - a)
            .slice(0, 10)
            .map(([student, hours]) => (
              <div key={student} className="flex items-center justify-between">
                <span className="text-sm font-medium text-gray-700">{student}</span>
                <div className="flex items-center space-x-2">
                  <div className="w-32 bg-gray-200 rounded-full h-2">
                    <div 
                      className="bg-purple-600 h-2 rounded-full" 
                      style={{ width: `${(hours / Math.max(...Object.values(analytics.hoursPerStudent))) * 100}%` }}
                    ></div>
                  </div>
                  <span className="text-sm text-gray-600 w-12">{hours.toFixed(1)}h</span>
                </div>
              </div>
            ))}
        </div>
      </div>
    </div>
  );
};

export default PlanningAnalytics; 