import React, { useState, useEffect, useRef } from 'react';
import { ClipboardList, FileText, Trash2, Printer, Edit, X, User, Search } from 'lucide-react';
import axios from 'axios';
import toast from 'react-hot-toast';
import EvaluationGrid from '../components/EvaluationGrid';
import EvaluationPrint from '../components/EvaluationPrint';
import { useSchoolYear } from '../contexts/SchoolYearContext';

const EvaluationsU52 = () => {
  const { selectedYear } = useSchoolYear();
  const [grids, setGrids] = useState([]);
  const [students, setStudents] = useState([]);
  const [classes, setClasses] = useState([]);
  const [selectedClass, setSelectedClass] = useState(null);
  const [selectedGrid, setSelectedGrid] = useState(null);
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [showEvaluationGrid, setShowEvaluationGrid] = useState(false);
  const [showCreateGrid, setShowCreateGrid] = useState(false);
  const [showPrintPreview, setShowPrintPreview] = useState(false);
  const [selectedGridForPrint, setSelectedGridForPrint] = useState(null);
  const printRef = useRef();

  useEffect(() => {
    fetchData();
  }, [selectedYear]);

  const fetchStudentsByClass = async (classId) => {
    try {
      const response = await axios.get(`/api/classes/${classId}/students`);
      if (response.data.success) {
        setStudents(response.data.data);
      }
    } catch (error) {
      console.error('Erreur lors du chargement des tudiants de la classe:', error);
      toast.error('Erreur lors du chargement des tudiants');
    }
  };

  const handleClassSelect = (classItem) => {
    setSelectedClass(classItem);
    fetchStudentsByClass(classItem.id);
  };

  const fetchData = async () => {
    try {
      setLoading(true);
      const [gridsRes, classesRes] = await Promise.all([
        axios.get('/api/evaluations/grids/all'),
        axios.get(`/api/classes?school_year=${selectedYear}`)
      ]);

      if (gridsRes.data.success) {
        // Filtrer seulement les grilles U52
        const u52Grids = gridsRes.data.data.filter(grid => grid.type === 'U52');
        
        // Trier les grilles par nom d'tudiant (ordre alphabtique)
        const sortedGrids = u52Grids.sort((a, b) => {
          const nameA = a.student_name.toLowerCase();
          const nameB = b.student_name.toLowerCase();
          return nameA.localeCompare(nameB);
        });
        
        // Recalculer les notes pour chaque grille
        const gridsWithRecalculatedScores = await Promise.all(
          sortedGrids.map(async (grid) => {
            try {
              const response = await axios.get(`/api/evaluations/student/${grid.student_id}`);
              
              if (response.data.success && response.data.data) {
                const evaluationsData = response.data.data.filter(evalData => 
                  evalData.type === 'U52'
                );
                
                if (evaluationsData.length > 0) {
                  const recalculatedScore = calculateStudentScore(evaluationsData);
                  return {
                    ...grid,
                    calculated_score: recalculatedScore.average,
                    has_evaluations: recalculatedScore.hasEvaluations
                  };
                }
              }
              
              return {
                ...grid,
                calculated_score: 0,
                has_evaluations: false
              };
            } catch (error) {
              console.error('Erreur lors du recalcul pour la grille:', grid.id, error);
              return {
                ...grid,
                calculated_score: 0,
                has_evaluations: false
              };
            }
          })
        );
        
        setGrids(gridsWithRecalculatedScores);
      }
      
      if (classesRes.data.success) {
        setClasses(classesRes.data.data);
      }
    } catch (error) {
      console.error('Erreur lors du chargement des donnes:', error);
      toast.error('Erreur lors du chargement des donnes');
    } finally {
      setLoading(false);
    }
  };

  const calculateStudentScore = (evaluations) => {
    if (!evaluations || evaluations.length === 0) {
      return { average: 0, hasEvaluations: false };
    }

    // Grouper par session d'valuation
    const sessionGroups = {};
    evaluations.forEach(evaluation => {
      if (!sessionGroups[evaluation.evaluated_at]) {
        sessionGroups[evaluation.evaluated_at] = [];
      }
      sessionGroups[evaluation.evaluated_at].push(evaluation);
    });
    
    // Calculer la moyenne de chaque session complte
    const sessionAverages = Object.values(sessionGroups)
      .filter(sessionEvals => sessionEvals.length >= 4) // Session complte
      .map(sessionEvals => {
        const totalScore = sessionEvals.reduce((sum, evaluation) => sum + (evaluation.score || 0), 0);
        return totalScore / sessionEvals.length;
      });
    
    if (sessionAverages.length === 0) {
      return { average: 0, hasEvaluations: false };
    }
    
    // Moyenne des sessions
    const overallAverage = sessionAverages.reduce((sum, avg) => sum + avg, 0) / sessionAverages.length;
    
    return { 
      average: overallAverage, 
      hasEvaluations: true,
      sessionCount: sessionAverages.length
    };
  };

  const handleCreateGrid = (student) => {
    setSelectedStudent(student);
    setShowCreateGrid(true);
  };

  const handleEditGrid = (grid) => {
    setSelectedGrid(grid);
    setShowEvaluationGrid(true);
  };

  const handleDeleteGrid = async (gridId) => {
    if (!window.confirm('tes-vous sr de vouloir supprimer cette grille d\'valuation ?')) {
      return;
    }

    try {
      await axios.delete(`/api/evaluations/grid/${gridId}`);
      toast.success('Grille supprime avec succs');
      fetchData();
    } catch (error) {
      console.error('Erreur lors de la suppression:', error);
      toast.error('Erreur lors de la suppression');
    }
  };

  const handlePrintGrid = (grid) => {
    setSelectedGridForPrint(grid);
          setShowPrintPreview(true);
  };

  const handlePrintConfirm = () => {
    if (printRef.current) {
      window.print();
    }
  };

  const filteredStudents = students.filter(student =>
    student.first_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    student.last_name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const filteredGrids = grids.filter(grid =>
    grid.student_name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
      <div className="flex items-center justify-between">
        <div>
              <h1 className="text-3xl font-bold text-gray-900 flex items-center">
                <ClipboardList className="h-8 w-8 mr-3 text-blue-600" />
                valuations U52
              </h1>
              <p className="mt-2 text-gray-600">
                Gestion des valuations de comptences U52
          </p>
        </div>
          </div>
      </div>

      {/* Slection de classe */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 mb-6">
          <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
            <User className="h-5 w-5 mr-2" />
            Slectionner une classe
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {classes.map((classItem) => (
              <button
                key={classItem.id}
                onClick={() => handleClassSelect(classItem)}
                className={`p-4 rounded-lg border-2 text-left transition-colors ${
                  selectedClass?.id === classItem.id
                    ? 'border-blue-500 bg-blue-50'
                    : 'border-gray-200 hover:border-gray-300'
                }`}
              >
                <div className="font-medium text-gray-900">{classItem.name}</div>
                <div className="text-sm text-gray-500">
                  {classItem.student_count} tudiant{classItem.student_count > 1 ? 's' : ''}
              </div>
              </button>
            ))}
          </div>
        </div>

      {selectedClass && (
          <>
            {/* Liste des tudiants */}
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 mb-6">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-lg font-semibold text-gray-900 flex items-center">
                  <User className="h-5 w-5 mr-2" />
                  tudiants - {selectedClass.name}
                </h2>
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <input
                  type="text"
                  placeholder="Rechercher un tudiant..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                    className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
            </div>
          </div>
          
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {filteredStudents.map((student) => {
                  const existingGrid = grids.find(grid => grid.student_id === student.id);
              return (
                    <div key={student.id} className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow">
                      <div className="flex items-center justify-between mb-2">
                        <div>
                          <h3 className="font-medium text-gray-900">
                          {student.first_name} {student.last_name}
                          </h3>
                          <p className="text-sm text-gray-500">{student.email}</p>
                        </div>
                        {existingGrid && (
                          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                            valu
                          </span>
                        )}
                      </div>
                      
                      {existingGrid && (
                        <div className="mb-3">
                          <div className="text-sm text-gray-600">
                            Note: <span className="font-semibold text-blue-600">
                              {existingGrid.calculated_score?.toFixed(1) || '0.0'}/20
                            </span>
                          </div>
                        </div>
                      )}

                      <div className="flex space-x-2">
                        {existingGrid ? (
                          <>
                            <button
                              onClick={() => handleEditGrid(existingGrid)}
                              className="flex-1 bg-blue-600 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-blue-700 transition-colors flex items-center justify-center"
                            >
                              <Edit className="h-4 w-4 mr-1" />
                              Modifier
                            </button>
                            <button
                              onClick={() => handlePrintGrid(existingGrid)}
                              className="bg-gray-600 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-gray-700 transition-colors"
                            >
                              <Printer className="h-4 w-4" />
                            </button>
                            <button
                              onClick={() => handleDeleteGrid(existingGrid.id)}
                              className="bg-red-600 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-red-700 transition-colors"
                            >
                              <Trash2 className="h-4 w-4" />
                            </button>
                          </>
                        ) : (
                          <button
                            onClick={() => handleCreateGrid(student)}
                            className="w-full bg-green-600 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-green-700 transition-colors flex items-center justify-center"
                          >
                            <FileText className="h-4 w-4 mr-1" />
                            Crer valuation
                          </button>
                        )}
                          </div>
                        </div>
                  );
                })}
                            </div>
                          </div>
                          
            {/* Grilles existantes */}
            {filteredGrids.length > 0 && (
              <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
                <h2 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                  <ClipboardList className="h-5 w-5 mr-2" />
                  Grilles d'valuation existantes
                </h2>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                  {filteredGrids.map((grid) => (
                    <div key={grid.id} className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow">
                      <div className="flex items-center justify-between mb-2">
                        <h3 className="font-medium text-gray-900">{grid.student_name}</h3>
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                          U52
                        </span>
                          </div>
                          
                      <div className="mb-3">
                        <div className="text-sm text-gray-600">
                          Note: <span className="font-semibold text-blue-600">
                            {grid.calculated_score?.toFixed(1) || '0.0'}/20
                          </span>
                        </div>
                        <div className="text-xs text-gray-500">
                          Cre le {new Date(grid.created_at).toLocaleDateString()}
                        </div>
                      </div>

                      <div className="flex space-x-2">
                        <button
                          onClick={() => handleEditGrid(grid)}
                          className="flex-1 bg-blue-600 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-blue-700 transition-colors flex items-center justify-center"
                        >
                          <Edit className="h-4 w-4 mr-1" />
                          Modifier
                        </button>
                        <button
                          onClick={() => handlePrintGrid(grid)}
                          className="bg-gray-600 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-gray-700 transition-colors"
                        >
                          <Printer className="h-4 w-4" />
                        </button>
                        <button
                          onClick={() => handleDeleteGrid(grid.id)}
                          className="bg-red-600 text-white px-3 py-2 rounded-md text-sm font-medium hover:bg-red-700 transition-colors"
                        >
                          <Trash2 className="h-4 w-4" />
                        </button>
                      </div>
                    </div>
                  ))}
            </div>
              </div>
            )}
          </>
          )}

        {/* Modal de grille d'valuation */}
        {showEvaluationGrid && selectedGrid && (
          <div className="fixed inset-0 z-50 overflow-y-auto">
            <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
              <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowEvaluationGrid(false)}></div>
              <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-4xl sm:w-full">
                <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                  <div className="flex items-center justify-between mb-4">
                    <h3 className="text-lg font-medium text-gray-900">Grille d'valuation U52</h3>
                    <button
                      onClick={() => setShowEvaluationGrid(false)}
                      className="text-gray-400 hover:text-gray-600"
                    >
                      <X className="h-6 w-6" />
                    </button>
                  </div>
                  <EvaluationGrid
                    student={selectedGrid}
                    onClose={() => setShowEvaluationGrid(false)}
                    onSave={() => {
                      setShowEvaluationGrid(false);
                      fetchData();
                    }}
                  />
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Modal de cration de grille */}
        {showCreateGrid && selectedStudent && (
          <div className="fixed inset-0 z-50 overflow-y-auto">
            <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
              <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowCreateGrid(false)}></div>
              <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-4xl sm:w-full">
                <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                  <div className="flex items-center justify-between mb-4">
                      <h3 className="text-lg font-medium text-gray-900">
                      Nouvelle grille d'valuation U52 - {selectedStudent.first_name} {selectedStudent.last_name}
                      </h3>
                    <button
                      onClick={() => setShowCreateGrid(false)}
                      className="text-gray-400 hover:text-gray-600"
                    >
                      <X className="h-6 w-6" />
                    </button>
                  </div>
                    <EvaluationGrid 
                    student={selectedStudent}
                    onClose={() => setShowCreateGrid(false)}
                    onSave={() => {
                      setShowCreateGrid(false);
                      fetchData();
                    }}
                  />
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Modal de prvisualisation d'impression */}
        {showPrintPreview && selectedGridForPrint && (
          <div className="fixed inset-0 z-50 overflow-y-auto">
            <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
              <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowPrintPreview(false)}></div>
              <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-4xl sm:w-full">
                <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                  <div className="flex items-center justify-between mb-4">
                    <h3 className="text-lg font-medium text-gray-900">Prvisualisation d'impression</h3>
                    <button
                      onClick={() => setShowPrintPreview(false)}
                      className="text-gray-400 hover:text-gray-600"
                    >
                      <X className="h-6 w-6" />
                    </button>
                  </div>
                  
                  <div ref={printRef} className="print-content">
                    <EvaluationPrint grid={selectedGridForPrint} />
                  </div>
                </div>
                
                <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                  <button
                    type="button"
                    onClick={handlePrintConfirm}
                    className="btn btn-primary sm:ml-3 sm:w-auto flex items-center"
                  >
                    <Printer className="h-4 w-4 mr-2" />
                    Imprimer
                  </button>
                  <button
                    type="button"
                    className="btn btn-secondary sm:ml-3 sm:w-auto"
                    onClick={() => {
                      setShowPrintPreview(false);
                      setSelectedGridForPrint(null);
                    }}
                  >
                    Annuler
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
      </div>
    );
};

export default EvaluationsU52;