import React, { useState, useEffect } from 'react';
import { Plus, Edit, Trash2, Users, User } from 'lucide-react';
import axios from 'axios';
import toast from 'react-hot-toast';

const Groups = () => {
  const [groups, setGroups] = useState([]);
  const [students, setStudents] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showGroupModal, setShowGroupModal] = useState(false);
  const [showBulkAssignModal, setShowBulkAssignModal] = useState(false);
  const [editingGroup, setEditingGroup] = useState(null);
  const [groupFormData, setGroupFormData] = useState({
    name: '',
    project_id: ''
  });

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const [groupsRes, studentsRes, projectsRes] = await Promise.all([
        axios.get('/api/students/groups/all'),
        axios.get('/api/students'),
        axios.get('/api/projects')
      ]);

      if (groupsRes.data.success) {
        setGroups(groupsRes.data.data);
        // console.log('Groupes chargs:', groupsRes.data.data);
      }
      if (studentsRes.data.success) {
        setStudents(studentsRes.data.data);
        // console.log('tudiants chargs:', studentsRes.data.data);
        // console.log('tudiants sans groupe:', studentsRes.data.data.filter(s => !s.group_id));
      }
      if (projectsRes.data.success) {
        setProjects(projectsRes.data.data);
        // console.log('Projets chargs:', projectsRes.data.data);
      }
    } catch (error) {
      console.error('Erreur lors du chargement des donnes:', error);
      toast.error('Erreur lors du chargement des donnes');
    } finally {
      setLoading(false);
    }
  };

  const handleGroupSubmit = async (e) => {
    e.preventDefault();
    
    try {
      if (editingGroup) {
        await axios.put(`/api/students/groups/${editingGroup.id}`, groupFormData);
        toast.success('Groupe mis  jour avec succs');
      } else {
        await axios.post('/api/students/groups', groupFormData);
        toast.success('Groupe cr avec succs');
      }
      setShowGroupModal(false);
      setEditingGroup(null);
      setGroupFormData({ name: '', project_id: '' });
      fetchData();
    } catch (error) {
      console.error('Erreur lors de la sauvegarde du groupe:', error);
      toast.error(error.response?.data?.message || 'Erreur lors de la sauvegarde du groupe');
    }
  };

  const handleEditGroup = (group) => {
    setEditingGroup(group);
    setGroupFormData({
      name: group.name,
      project_id: group.project_id || ''
    });
    setShowGroupModal(true);
  };

  const handleDeleteGroup = async (groupId) => {
    const group = groups.find(g => g.id === groupId);
    const groupStudents = students.filter(s => s.group_id === groupId);
    
    if (!window.confirm(`tes-vous sr de vouloir supprimer le groupe "${group?.name}" ?\n\n${groupStudents.length} tudiant(s) seront retir(s) du groupe et redeviendront disponibles pour une nouvelle affectation.`)) {
      return;
    }

    try {
      const response = await axios.delete(`/api/students/groups/${groupId}`);
      toast.success(response.data.message || 'Groupe supprim avec succs');
      fetchData();
    } catch (error) {
      console.error('Erreur lors de la suppression du groupe:', error);
      toast.error(error.response?.data?.message || 'Erreur lors de la suppression du groupe');
    }
  };

  const handleBulkAssign = async (studentIds, groupId) => {
    try {
      await axios.post('/api/students/bulk-assign-group', {
        student_ids: studentIds,
        group_id: groupId
      });
      toast.success(`${studentIds.length} tudiant(s) affect(s) au groupe avec succs`);
      setShowBulkAssignModal(false);
      fetchData();
    } catch (error) {
      console.error('Erreur lors de l\'affectation:', error);
      toast.error('Erreur lors de l\'affectation des tudiants');
    }
  };

  const handleResetAllStudents = async () => {
    try {
      await axios.post('/api/students/reset-all-groups');
      toast.success('Tous les tudiants ont t retirs de leurs groupes');
      fetchData();
    } catch (error) {
      console.error('Erreur lors de la rinitialisation:', error);
      toast.error('Erreur lors de la rinitialisation des groupes');
    }
  };

  const handleTestGroupAssignment = async () => {
    try {
      // Crer un groupe de test
      const groupResponse = await axios.post('/api/students/groups', {
        name: 'Groupe Test',
        project_id: ''
      });
      
      if (groupResponse.data.success) {
        const groupId = groupResponse.data.data.id;
        
        // Affecter les premiers tudiants disponibles
        const availableStudents = students.filter(s => !s.group_id).slice(0, 5);
        if (availableStudents.length > 0) {
          await axios.post('/api/students/bulk-assign-group', {
            student_ids: availableStudents.map(s => s.id),
            group_id: groupId
          });
          toast.success(`Groupe de test cr avec ${availableStudents.length} tudiants`);
        } else {
          toast.success('Groupe de test cr (aucun tudiant disponible)');
        }
        fetchData();
      }
    } catch (error) {
      console.error('Erreur lors du test:', error);
      toast.error('Erreur lors du test');
    }
  };

  const handleCreateSampleGroups = async () => {
    try {
      // Crer 3 groupes de test
      const groupNames = ['Groupe A', 'Groupe B', 'Groupe C'];
      const createdGroups = [];
      
      for (const name of groupNames) {
        const groupResponse = await axios.post('/api/students/groups', {
          name: name,
          project_id: ''
        });
        if (groupResponse.data.success) {
          createdGroups.push(groupResponse.data.data);
        }
      }
      
      // Affecter des tudiants  chaque groupe
      const availableStudents = students.filter(s => !s.group_id);
      const studentsPerGroup = Math.floor(availableStudents.length / createdGroups.length);
      
      for (let i = 0; i < createdGroups.length; i++) {
        const startIndex = i * studentsPerGroup;
        const endIndex = i === createdGroups.length - 1 ? availableStudents.length : startIndex + studentsPerGroup;
        const groupStudents = availableStudents.slice(startIndex, endIndex);
        
        if (groupStudents.length > 0) {
          await axios.post('/api/students/bulk-assign-group', {
            student_ids: groupStudents.map(s => s.id),
            group_id: createdGroups[i].id
          });
        }
      }
      
      toast.success(`${createdGroups.length} groupes crs avec des tudiants`);
      fetchData();
    } catch (error) {
      console.error('Erreur lors de la cration des groupes:', error);
      toast.error('Erreur lors de la cration des groupes');
    }
  };

  const handleFixGroupAssignments = async () => {
    try {
      // Rcuprer tous les tudiants avec un group_id mais sans groupe existant
      const studentsWithInvalidGroup = students.filter(s => s.group_id && !groups.find(g => g.id === s.group_id));
      
      if (studentsWithInvalidGroup.length > 0) {
        // Crer un groupe pour ces tudiants
        const groupResponse = await axios.post('/api/students/groups', {
          name: 'Groupe Orphelin',
          project_id: ''
        });
        
        if (groupResponse.data.success) {
          const groupId = groupResponse.data.data.id;
          
          // Affecter ces tudiants au nouveau groupe
          await axios.post('/api/students/bulk-assign-group', {
            student_ids: studentsWithInvalidGroup.map(s => s.id),
            group_id: groupId
          });
          
          toast.success(`Groupe cr pour ${studentsWithInvalidGroup.length} tudiants orphelins`);
          fetchData();
        }
      } else {
        toast.success('Aucun tudiant orphelin trouv');
      }
    } catch (error) {
      console.error('Erreur lors de la correction:', error);
      toast.error('Erreur lors de la correction');
    }
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* En-tte */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Gestion des groupes</h1>
          <p className="mt-1 text-sm text-gray-500">
            Crez et grez les groupes d'tudiants
          </p>
        </div>
        <div className="flex space-x-3">
          <button
            onClick={() => setShowBulkAssignModal(true)}
            className="btn btn-secondary flex items-center"
            disabled={students.filter(s => !s.group_id).length === 0}
            title={students.filter(s => !s.group_id).length === 0 ? 'Aucun tudiant sans groupe' : `Affecter ${students.filter(s => !s.group_id).length} tudiant(s) sans groupe`}
          >
            <User className="h-4 w-4 mr-2" />
            Affecter des tudiants ({students.filter(s => !s.group_id).length})
          </button>
          {students.filter(s => s.group_id).length > 0 && (
            <button
              onClick={() => {
                if (window.confirm(`tes-vous sr de vouloir retirer tous les tudiants de leurs groupes ?\n\n${students.filter(s => s.group_id).length} tudiant(s) seront retir(s) de leurs groupes respectifs.`)) {
                  handleResetAllStudents();
                }
              }}
              className="btn btn-warning flex items-center"
              title="Retirer tous les tudiants de leurs groupes"
            >
              <Trash2 className="h-4 w-4 mr-2" />
              Rinitialiser tous ({students.filter(s => s.group_id).length})
            </button>
          )}
          <button
            onClick={() => setShowGroupModal(true)}
            className="btn btn-primary flex items-center"
          >
            <Plus className="h-4 w-4 mr-2" />
            Nouveau groupe
          </button>
          <button
            onClick={handleTestGroupAssignment}
            className="btn btn-secondary flex items-center"
            title="Crer un groupe de test avec des tudiants"
          >
            <User className="h-4 w-4 mr-2" />
            Test
          </button>
          <button
            onClick={handleCreateSampleGroups}
            className="btn btn-success flex items-center"
            title="Crer 3 groupes avec des tudiants"
          >
            <Users className="h-4 w-4 mr-2" />
            Crer 3 groupes
          </button>
          <button
            onClick={handleFixGroupAssignments}
            className="btn btn-warning flex items-center"
            title="Corriger les affectations de groupes orphelins"
          >
            <User className="h-4 w-4 mr-2" />
            Corriger
          </button>
        </div>
      </div>

      {/* Statistiques */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="bg-white overflow-hidden shadow rounded-lg">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <Users className="h-6 w-6 text-gray-400" />
              </div>
              <div className="ml-5 w-0 flex-1">
                <dl>
                  <dt className="text-sm font-medium text-gray-500 truncate">
                    Total groupes
                  </dt>
                  <dd className="text-lg font-medium text-gray-900">
                    {groups.length}
                  </dd>
                </dl>
              </div>
            </div>
          </div>
        </div>

        <div className="bg-white overflow-hidden shadow rounded-lg">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <User className="h-6 w-6 text-gray-400" />
              </div>
              <div className="ml-5 w-0 flex-1">
                <dl>
                  <dt className="text-sm font-medium text-gray-500 truncate">
                    Total tudiants
                  </dt>
                  <dd className="text-lg font-medium text-gray-900">
                    {students.length}
                  </dd>
                </dl>
              </div>
            </div>
          </div>
        </div>

        <div className="bg-white overflow-hidden shadow rounded-lg">
          <div className="p-5">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <User className="h-6 w-6 text-gray-400" />
              </div>
              <div className="ml-5 w-0 flex-1">
                <dl>
                  <dt className="text-sm font-medium text-gray-500 truncate">
                    Sans groupe
                  </dt>
                  <dd className="text-lg font-medium text-gray-900">
                    {students.filter(s => !s.group_id).length}
                  </dd>
                </dl>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Liste des groupes */}
      <div className="bg-white shadow overflow-hidden sm:rounded-md">
        <div className="px-4 py-5 sm:px-6 border-b border-gray-200">
          <h3 className="text-lg leading-6 font-medium text-gray-900">
            Gestion des groupes
          </h3>
          <p className="mt-1 max-w-2xl text-sm text-gray-500">
            Crez et grez les groupes d'tudiants
          </p>
        </div>
        
        {groups.length === 0 ? (
          <div className="text-center py-12">
            <Users className="mx-auto h-12 w-12 text-gray-400" />
            <h3 className="mt-2 text-sm font-medium text-gray-900">Aucun groupe</h3>
            <p className="mt-1 text-sm text-gray-500">
              Crez votre premier groupe pour organiser les tudiants.
            </p>
            <div className="mt-6">
              <button
                onClick={() => setShowGroupModal(true)}
                className="btn btn-primary"
              >
                <Plus className="h-4 w-4 mr-2" />
                Crer un groupe
              </button>
            </div>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Groupe
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Projet associ
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    tudiants
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider w-48">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {groups.map((group) => {
                  const groupStudents = students.filter(s => s.group_id === group.id);
                  // console.log(`Groupe ${group.name} (ID: ${group.id}):`, groupStudents.length, 'tudiants');
                  // console.log('tudiants du groupe:', groupStudents);
                  // console.log('Tous les tudiants avec group_id:', students.filter(s => s.group_id));
                  return (
                    <React.Fragment key={group.id}>
                      <tr className="hover:bg-gray-50">
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="text-sm font-medium text-gray-900">
                            {group.name}
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                          {group.project_title || 'Aucun projet'}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                          <div className="flex items-center">
                            <Users className="h-4 w-4 mr-2 text-gray-400" />
                            {groupStudents.length} tudiant{groupStudents.length > 1 ? 's' : ''}
                          </div>
                          {groupStudents.length === 0 && (
                            <div className="text-xs text-gray-500 mt-1">
                              Aucun tudiant assign
                            </div>
                          )}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                          <div className="flex space-x-2">
                            <button
                              onClick={() => handleEditGroup(group)}
                              className="inline-flex items-center px-2 py-1 border border-transparent text-xs font-medium rounded text-primary-700 bg-primary-100 hover:bg-primary-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500"
                              title="Modifier le groupe"
                            >
                              <Edit className="h-3 w-3 mr-1" />
                              Modifier
                            </button>
                            {groupStudents.length === 0 && students.filter(s => !s.group_id).length > 0 && (
                              <button
                                onClick={() => {
                                  setShowBulkAssignModal(true);
                                  // Pr-slectionner ce groupe
                                  setTimeout(() => {
                                    const select = document.getElementById('bulk-group-select');
                                    if (select) select.value = group.id;
                                  }, 100);
                                }}
                                className="inline-flex items-center px-2 py-1 border border-transparent text-xs font-medium rounded text-green-700 bg-green-100 hover:bg-green-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500"
                                title="Affecter des tudiants  ce groupe"
                              >
                                <User className="h-3 w-3 mr-1" />
                                Affecter
                              </button>
                            )}
                            <button
                              onClick={() => handleDeleteGroup(group.id)}
                              className="inline-flex items-center px-2 py-1 border border-transparent text-xs font-medium rounded text-red-700 bg-red-100 hover:bg-red-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500"
                              title="Supprimer le groupe"
                            >
                              <Trash2 className="h-3 w-3 mr-1" />
                              Supprimer
                            </button>
                          </div>
                        </td>
                      </tr>
                                             {/* Affichage des tudiants sous le groupe */}
                       <tr className="bg-gray-50">
                         <td colSpan="4" className="px-6 py-3">
                           {groupStudents.length > 0 ? (
                             <div className="text-sm text-gray-600">
                               <div className="font-medium mb-2 text-gray-800">tudiants du groupe :</div>
                               <div className="space-y-1">
                                 {groupStudents.map((student) => (
                                   <div key={student.id} className="flex items-center space-x-2 bg-white px-3 py-2 rounded border">
                                     <User className="h-4 w-4 text-blue-500" />
                                     <span className="font-medium text-gray-900">
                                       {student.first_name} {student.last_name}
                                     </span>
                                     {student.role && (
                                       <span className="text-xs text-gray-500 bg-gray-100 px-2 py-1 rounded">
                                         {student.role}
                                       </span>
                                     )}
                                   </div>
                                 ))}
                               </div>
                             </div>
                           ) : (
                             <div className="text-sm text-gray-500 text-center py-2">
                               <User className="h-4 w-4 mx-auto mb-1 text-gray-400" />
                               Aucun tudiant assign  ce groupe
                             </div>
                           )}
                         </td>
                       </tr>
                    </React.Fragment>
                  );
                })}
              </tbody>
            </table>
          </div>
        )}
      </div>

      {/* Section de dbogage */}
      <div className="bg-gray-100 p-4 rounded-lg">
        <h4 className="font-medium text-gray-900 mb-2">Dbogage</h4>
        <div className="text-sm text-gray-600 space-y-1">
          <div>Total groupes: {groups.length}</div>
          <div>Total tudiants: {students.length}</div>
          <div>tudiants avec groupe: {students.filter(s => s.group_id).length}</div>
          <div>tudiants sans groupe: {students.filter(s => !s.group_id).length}</div>
          <div className="mt-2">
            <strong>Groupes:</strong>
            {groups.map(group => (
              <div key={group.id} className="ml-4">
                - {group.name} (ID: {group.id}) - {students.filter(s => s.group_id === group.id).length} tudiants
              </div>
            ))}
          </div>
          <div className="mt-2">
            <strong>tudiants avec group_id:</strong>
            {students.filter(s => s.group_id).map(student => (
              <div key={student.id} className="ml-4">
                - {student.first_name} {student.last_name} (group_id: {student.group_id}, group_name: {student.group_name || 'null'})
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Modal Groupe */}
      {showGroupModal && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowGroupModal(false)} />
            
            <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
              <form onSubmit={handleGroupSubmit}>
                <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                  <div className="mb-4">
                    <h3 className="text-lg font-medium text-gray-900">
                      {editingGroup ? 'Modifier le groupe' : 'Nouveau groupe'}
                    </h3>
                  </div>
                  
                  <div className="space-y-4">
                    <div>
                      <label className="label">Nom du groupe *</label>
                      <input
                        type="text"
                        required
                        className="input"
                        placeholder="Groupe A"
                        value={groupFormData.name}
                        onChange={(e) => setGroupFormData({...groupFormData, name: e.target.value})}
                      />
                    </div>
                    
                    <div>
                      <label className="label">Projet associ</label>
                      <select
                        className="input"
                        value={groupFormData.project_id}
                        onChange={(e) => setGroupFormData({...groupFormData, project_id: e.target.value})}
                      >
                        <option value="">Aucun projet</option>
                        {projects.map((project) => (
                          <option key={project.id} value={project.id}>
                            {project.title}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                </div>
                
                <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                  <button
                    type="submit"
                    className="btn btn-primary sm:ml-3 sm:w-auto"
                  >
                    {editingGroup ? 'Mettre  jour' : 'Crer le groupe'}
                  </button>
                  <button
                    type="button"
                    className="btn btn-secondary sm:mt-0 sm:w-auto"
                    onClick={() => {
                      setShowGroupModal(false);
                      setEditingGroup(null);
                      setGroupFormData({ name: '', project_id: '' });
                    }}
                  >
                    Annuler
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      {/* Modal d'affectation en masse */}
      {showBulkAssignModal && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowBulkAssignModal(false)} />
            
            <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
              <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                <div className="mb-4">
                  <h3 className="text-lg font-medium text-gray-900">
                    Affecter des tudiants  un groupe
                  </h3>
                </div>
                
                <div className="space-y-4">
                  <div>
                    <label className="label">Groupe de destination</label>
                    <select
                      className="input"
                      id="bulk-group-select"
                    >
                      <option value="">Slectionner un groupe</option>
                      {groups.map((group) => (
                        <option key={group.id} value={group.id}>
                          {group.name}
                        </option>
                      ))}
                    </select>
                  </div>
                  
                  <div>
                    <label className="label">tudiants sans groupe ({students.filter(s => !s.group_id).length})</label>
                    <div className="max-h-40 overflow-y-auto border border-gray-300 rounded-md p-2">
                      {students.filter(s => !s.group_id).length === 0 ? (
                        <div className="text-sm text-gray-500 py-2 text-center">
                          Aucun tudiant sans groupe
                        </div>
                      ) : (
                        students.filter(s => !s.group_id).map((student) => (
                          <label key={student.id} className="flex items-center space-x-2 py-1">
                            <input
                              type="checkbox"
                              value={student.id}
                              className="rounded border-gray-300 text-primary-600 focus:ring-primary-500"
                            />
                            <span className="text-sm text-gray-900">
                              {student.first_name} {student.last_name}
                            </span>
                          </label>
                        ))
                      )}
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                <button
                  type="button"
                  className="btn btn-primary sm:ml-3 sm:w-auto"
                  onClick={() => {
                    const groupId = document.getElementById('bulk-group-select').value;
                    const selectedStudents = Array.from(document.querySelectorAll('input[type="checkbox"]:checked'))
                      .map(cb => cb.value);
                    
                    if (!groupId) {
                      toast.error('Veuillez slectionner un groupe');
                      return;
                    }
                    if (selectedStudents.length === 0) {
                      toast.error('Veuillez slectionner au moins un tudiant');
                      return;
                    }
                    
                    handleBulkAssign(selectedStudents, groupId);
                  }}
                >
                  Affecter les tudiants
                </button>
                <button
                  type="button"
                  className="btn btn-secondary sm:mt-0 sm:w-auto"
                  onClick={() => setShowBulkAssignModal(false)}
                >
                  Annuler
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Groups;
