import React, { useState } from 'react';
import { X, Save, Download } from 'lucide-react';

const TPSheetModal = ({ isOpen, onClose, onSave }) => {
  console.log(' TPSheetModal render - isOpen:', isOpen);
  const [content, setContent] = useState({
    title: 'FICHE DE TRAVAUX PRATIQUES',
    subtitle: 'Titre de la fiche',
    objective: 'Objectif :',
    equipment: 'Matriel ncessaire :',
    procedure: 'Procdure :',
    observations: 'Observations :',
    conclusion: 'Conclusion :'
  });

  const [editingZone, setEditingZone] = useState(null);

  const handleZoneClick = (zoneKey) => {
    setEditingZone(zoneKey);
  };

  const handleContentChange = (zoneKey, newContent) => {
    setContent(prev => ({
      ...prev,
      [zoneKey]: newContent
    }));
  };


  const handleSave = () => {
    onSave(content);
    onClose();
  };

  const handleDownload = () => {
    // Logique pour tlcharger la fiche en PDF
    console.log('Tlchargement de la fiche TP:', content);
  };

  if (!isOpen) {
    console.log(' Modal ferm - isOpen:', isOpen);
    return null;
  }

  console.log(' Modal ouvert - isOpen:', isOpen);
  return (
    <div className="fixed inset-0 z-50 overflow-y-auto" style={{ zIndex: 9999 }}>
      <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={onClose}></div>
        
        {/* Modal A4 Portrait */}
        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-4xl sm:w-full">
          {/* Header */}
          <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4 border-b">
            <div className="flex justify-between items-center">
              <h3 className="text-lg leading-6 font-medium text-gray-900">
                Crer une Fiche TP
              </h3>
              <div className="flex space-x-2">
                <button
                  onClick={handleDownload}
                  className="inline-flex items-center px-3 py-2 border border-gray-300 shadow-sm text-sm leading-4 font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  <Download className="h-4 w-4 mr-2" />
                  Tlcharger
                </button>
                <button
                  onClick={handleSave}
                  className="inline-flex items-center px-3 py-2 border border-transparent text-sm leading-4 font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  <Save className="h-4 w-4 mr-2" />
                  Sauvegarder
                </button>
                <button
                  onClick={onClose}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <X className="h-6 w-6" />
                </button>
              </div>
            </div>
          </div>

          {/* A4 Content */}
          <div className="bg-white px-4 pt-5 pb-4 sm:p-6">
            {/* A4 Portrait Container */}
            <div className="mx-auto bg-white shadow-lg" style={{ width: '210mm', height: '297mm', padding: '20mm' }}>
              {/* Header */}
              <div className="text-center mb-8">
                <h1 
                  className="text-2xl font-bold text-gray-900 mb-2 cursor-pointer hover:bg-gray-100 p-2 rounded"
                  onClick={() => handleZoneClick('title')}
                >
                  {editingZone === 'title' ? (
                    <input
                      type="text"
                      value={content.title}
                      onChange={(e) => handleContentChange('title', e.target.value)}
                      onBlur={() => setEditingZone(null)}
                      className="w-full text-center text-2xl font-bold border-none outline-none bg-transparent"
                      autoFocus
                    />
                  ) : (
                    content.title
                  )}
                </h1>
                <h2 
                  className="text-lg text-gray-700 cursor-pointer hover:bg-gray-100 p-2 rounded"
                  onClick={() => handleZoneClick('subtitle')}
                >
                  {editingZone === 'subtitle' ? (
                    <input
                      type="text"
                      value={content.subtitle}
                      onChange={(e) => handleContentChange('subtitle', e.target.value)}
                      onBlur={() => setEditingZone(null)}
                      className="w-full text-center text-lg border-none outline-none bg-transparent"
                      autoFocus
                    />
                  ) : (
                    content.subtitle
                  )}
                </h2>
              </div>

              {/* Content Sections */}
              <div className="space-y-6">
                {/* Objectif */}
                <div className="border-b pb-4">
                  <h3 
                    className="text-lg font-semibold text-gray-800 mb-3 cursor-pointer hover:bg-gray-100 p-2 rounded"
                    onClick={() => handleZoneClick('objective')}
                  >
                    {editingZone === 'objective' ? (
                      <input
                        type="text"
                        value={content.objective}
                        onChange={(e) => handleContentChange('objective', e.target.value)}
                        onBlur={() => setEditingZone(null)}
                        className="w-full text-lg font-semibold border-none outline-none bg-transparent"
                        autoFocus
                      />
                    ) : (
                      content.objective
                    )}
                  </h3>
                  <div className="ml-4">
                    <textarea
                      className="w-full h-20 p-2 border border-gray-300 rounded-md resize-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                      placeholder="Dcrivez l'objectif de la fiche TP..."
                      value={content.objective}
                      onChange={(e) => handleContentChange('objective', e.target.value)}
                    />
                  </div>
                </div>

                {/* Matriel */}
                <div className="border-b pb-4">
                  <h3 
                    className="text-lg font-semibold text-gray-800 mb-3 cursor-pointer hover:bg-gray-100 p-2 rounded"
                    onClick={() => handleZoneClick('equipment')}
                  >
                    {editingZone === 'equipment' ? (
                      <input
                        type="text"
                        value={content.equipment}
                        onChange={(e) => handleContentChange('equipment', e.target.value)}
                        onBlur={() => setEditingZone(null)}
                        className="w-full text-lg font-semibold border-none outline-none bg-transparent"
                        autoFocus
                      />
                    ) : (
                      content.equipment
                    )}
                  </h3>
                  <div className="ml-4">
                    <textarea
                      className="w-full h-24 p-2 border border-gray-300 rounded-md resize-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                      placeholder="Listez le matriel ncessaire..."
                      value={content.equipment}
                      onChange={(e) => handleContentChange('equipment', e.target.value)}
                    />
                  </div>
                </div>

                {/* Procdure */}
                <div className="border-b pb-4">
                  <h3 
                    className="text-lg font-semibold text-gray-800 mb-3 cursor-pointer hover:bg-gray-100 p-2 rounded"
                    onClick={() => handleZoneClick('procedure')}
                  >
                    {editingZone === 'procedure' ? (
                      <input
                        type="text"
                        value={content.procedure}
                        onChange={(e) => handleContentChange('procedure', e.target.value)}
                        onBlur={() => setEditingZone(null)}
                        className="w-full text-lg font-semibold border-none outline-none bg-transparent"
                        autoFocus
                      />
                    ) : (
                      content.procedure
                    )}
                  </h3>
                  <div className="ml-4">
                    <textarea
                      className="w-full h-32 p-2 border border-gray-300 rounded-md resize-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                      placeholder="Dcrivez la procdure tape par tape..."
                      value={content.procedure}
                      onChange={(e) => handleContentChange('procedure', e.target.value)}
                    />
                  </div>
                </div>

                {/* Observations */}
                <div className="border-b pb-4">
                  <h3 
                    className="text-lg font-semibold text-gray-800 mb-3 cursor-pointer hover:bg-gray-100 p-2 rounded"
                    onClick={() => handleZoneClick('observations')}
                  >
                    {editingZone === 'observations' ? (
                      <input
                        type="text"
                        value={content.observations}
                        onChange={(e) => handleContentChange('observations', e.target.value)}
                        onBlur={() => setEditingZone(null)}
                        className="w-full text-lg font-semibold border-none outline-none bg-transparent"
                        autoFocus
                      />
                    ) : (
                      content.observations
                    )}
                  </h3>
                  <div className="ml-4">
                    <textarea
                      className="w-full h-24 p-2 border border-gray-300 rounded-md resize-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                      placeholder="Notez vos observations..."
                      value={content.observations}
                      onChange={(e) => handleContentChange('observations', e.target.value)}
                    />
                  </div>
                </div>

                {/* Conclusion */}
                <div>
                  <h3 
                    className="text-lg font-semibold text-gray-800 mb-3 cursor-pointer hover:bg-gray-100 p-2 rounded"
                    onClick={() => handleZoneClick('conclusion')}
                  >
                    {editingZone === 'conclusion' ? (
                      <input
                        type="text"
                        value={content.conclusion}
                        onChange={(e) => handleContentChange('conclusion', e.target.value)}
                        onBlur={() => setEditingZone(null)}
                        className="w-full text-lg font-semibold border-none outline-none bg-transparent"
                        autoFocus
                      />
                    ) : (
                      content.conclusion
                    )}
                  </h3>
                  <div className="ml-4">
                    <textarea
                      className="w-full h-20 p-2 border border-gray-300 rounded-md resize-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
                      placeholder="Tirez vos conclusions..."
                      value={content.conclusion}
                      onChange={(e) => handleContentChange('conclusion', e.target.value)}
                    />
                  </div>
                </div>
              </div>

              {/* Footer */}
              <div className="mt-8 pt-4 border-t text-center text-sm text-gray-500">
                <p>Date : _______________</p>
                <p>Nom : _______________</p>
                <p>Signature : _______________</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TPSheetModal;
