import React from 'react';

const EvaluationPrint = ({ student, evaluations, globalScore, globalComments, competenceInfo, evaluationType, evaluationMode, directNotes }) => {
  console.log(' Mode d\'valuation:', evaluationMode);
  console.log(' DirectNotes:', directNotes);

  // Vrifier si les donnes sont valides
  if (!student) {
    console.log(' Donnes tudiant manquantes pour l\'impression:', { student });
    return (
      <div style={{ padding: '20px', textAlign: 'center' }}>
        <h3>Erreur d'impression</h3>
        <p>Donnes tudiant manquantes pour gnrer l'impression.</p>
      </div>
    );
  }

  // Si pas d'valuations, afficher un message mais continuer
  if (!evaluations || Object.keys(evaluations).length === 0) {
    console.log(' Aucune valuation trouve, affichage avec donnes vides');
  }

  const formatDate = (date) => {
    return new Date().toLocaleDateString('fr-FR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    });
  };

  // Dfinir les descriptions des comptences
  const competenceDescriptions = {
    'C1': 'Recenser et prendre en compte les normes, les rglementations applicables au projet/chantier',
    'C3': 'Grer les risques et les alas lis  la ralisation des tches',
    'C12': 'Grer et conduire (y compris avec les documents de : organisation, planification, suivi, pilotage, rception etc.) le projet/chantier professionnel'
  };

  // Dfinir les indicateurs par comptence
  const indicators = {
    'C1': [
      'Les contraintes et ressources normatives et rglementaires dont celles lies  la qualit, la scurit, la sant et l\'environnement sont prises en compte tout au long du projet/chantier',
      'Les tches sont rparties en fonction des habilitations, des certifications des quipiers en tenant compte du planning des autres intervenants (monteur-cbleurs, autres corps d\'tat, sous-traitants)',
      'Les intervenants sont informs sur les rgles lies  la sant, la scurit et l\'environnement applicable dans le cadre du projet/chantier',
      'La bonne utilisation des dispositifs de protection des personnes et des biens est contrle'
    ],
    'C3': [
      'Les diffrentes tapes de l\'activit sont adaptes pour tenir compte des nouvelles solutions retenues et des circonstances',
      'Des solutions pour pallier les alas sont proposes  la hirarchie',
      'La mise en uvre des mesures de prvention est prvue',
      'La prsence des dispositifs de protection des personnes et des biens est contrle',
      'Les dispositifs de protection des personnes et des biens sont utiliss',
      'Les intervenants sont informs sur les rgles lies  la sant, la scurit et l\'environnement, applicables dans le cadre du projet/chantier',
      'Toutes les rgles de sant, de scurit et d\'environnement sont respectes tout au long du projet/chantier',
      'Les risques lis  l\'activit sont identifis et recenss',
      'Des solutions pour prvenir les risques sont proposes et valides',
      'Les alas sont grs'
    ],
    'C12': [
      'Les informations crites et orales relatives au projet/chantier sont collectes',
      'La liste et la disponibilit des matriels, quipements, outillages et outils numriques ncessaires sont vrifies',
      'L\'intervention est planifie',
      'La dure du projet/chantier est value',
      'Les approvisionnements sont planifis',
      'Le projet/chantier est organis pour qu\'il soit men de manire co-responsable',
      'Les tches sont rparties en fonction des habilitations, des certifications des quipiers en tenant compte du planning des autres intervenants',
      'Les revues de projet sont effectues',
      'Les situations imprvues, dlicates, conflictuelles sont gres en lien avec la hirarchie',
      'Les arbitrages ncessaires en fonction des contraintes rencontres sont pris',
      'Les diffrentes tapes du planning sont respectes ou adaptes',
      'La qualit et la conformit des travaux raliss, y compris par la sous-traitance, sont contrles',
      'Les indicateurs (cots, dlais, qualit) de suivi du projet/chantier sont renseigns',
      'Les intervenants sont informs sur les rgles lies  la sant, la scurit et l\'environnement, applicables dans le cadre du projet/chantier',
      'Les dispositifs de protection des personnes et des biens sont prsents et bien utiliss',
      'Les rgles de sant, de scurit et environnementales sont respectes tout au long du projet/chantier',
      'L\'quipe est anime pour mener  bien le projet/chantier'
    ]
  };

  // Fonction pour calculer la moyenne des croix
  const calculateAverageLevel = (competence) => {
    // Si on a des directNotes (valuation en mode direct), les utiliser pour la ligne de moyenne
    if (directNotes && directNotes[competence]) {
      const directData = directNotes[competence];
      if (directData.N1) return 1;
      if (directData.N2) return 2;
      if (directData.N3) return 3;
      if (directData.N4) return 4;
      return 0;
    }

    // Sinon, utiliser la logique existante pour le mode critres
    const data = evaluations[competence];
    if (!data || !data.criteria) return 0;

    const positions = [];
    Object.values(data.criteria).forEach(criterion => {
      if (criterion.evaluated) {
        if (criterion.N1) positions.push(1);
        else if (criterion.N2) positions.push(2);
        else if (criterion.N3) positions.push(3);
        else if (criterion.N4) positions.push(4);
      }
    });

    if (positions.length === 0) return 0;
    const average = positions.reduce((sum, pos) => sum + pos, 0) / positions.length;
    return Math.floor(average);
  };


  return (
    <div className="print-container" style={{ 
      width: '210mm', 
      height: '297mm', 
      margin: '0 auto', 
      padding: '8mm 6mm 8mm 6mm',
      backgroundColor: 'white',
      fontFamily: 'Arial, sans-serif',
      fontSize: '9pt',
      lineHeight: '1.1'
    }}>
      {/* En-tte */}
      <div style={{ 
        textAlign: 'center', 
        marginBottom: '2mm',
        borderBottom: '1px solid #1e40af',
        paddingBottom: '1mm'
      }}>
        <h1 style={{ 
          fontSize: '14pt', 
          fontWeight: 'bold', 
          margin: '0 0 1mm 0',
          color: '#1e40af'
        }}>
          GRILLE D'VALUATION U52
        </h1>
        <div style={{ fontSize: '10pt', color: '#6b7280' }}>
          valuation des comptences professionnelles
        </div>
      </div>

      {/* Nom de l'tudiant en vidence */}
      <div style={{ 
        textAlign: 'center',
        marginBottom: '3mm',
        padding: '2mm',
        backgroundColor: '#eff6ff',
        border: '2px solid #3b82f6',
        borderRadius: '2mm'
      }}>
        <div style={{ 
          fontSize: '16pt', 
          fontWeight: 'bold', 
          color: '#1e40af',
          textTransform: 'uppercase'
        }}>
          {student.firstName || student.student_name || 'N/A'} {student.lastName || ''}
        </div>
      </div>

      {/* Informations tudiant */}
      <div style={{ 
        marginBottom: '2mm',
        padding: '1mm',
        backgroundColor: '#f8fafc',
        border: '1px solid #e2e8f0',
        borderRadius: '1mm'
      }}>
        <div style={{ fontSize: '9pt', fontWeight: 'bold', marginBottom: '1mm' }}>
          Informations de l'valuation
        </div>
        <div style={{ fontSize: '8pt', display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '1mm' }}>
          <div><strong>tudiant :</strong> <span style={{ fontSize: '12pt', fontWeight: 'bold' }}>{student.firstName || student.student_name || 'N/A'} {student.lastName || ''}</span></div>
          <div><strong>Date :</strong> {formatDate()}</div>
          <div><strong>Nom de l'activit :</strong> {student.activity_name || (() => {
            // Chercher dans toutes les comptences
            for (const competence of ['C1', 'C3', 'C12']) {
              if (evaluations[competence]?.activity_name) {
                return evaluations[competence].activity_name;
              }
            }
            return 'Non spcifi';
          })()}</div>
          <div><strong>Type d'activit :</strong> {(() => {
            const activityType = student.activity_type || (() => {
              // Chercher dans toutes les comptences
              for (const competence of ['C1', 'C3', 'C12']) {
                if (evaluations[competence]?.activity_type) {
                  return evaluations[competence].activity_type;
                }
              }
              return null;
            })();
            
            // Convertir les libells en libells courts
            if (activityType === 'entreprise' || activityType === 'Activits observes en entreprise') {
              return 'En entreprise';
            } else if (activityType === 'centre' || activityType === 'Activits observes en centre de formation') {
              return 'En centre de formation';
            } else if (activityType) {
              return activityType;
            } else {
              return 'Non spcifi';
            }
          })()}</div>
        </div>
        
      </div>

      {/* Comptences */}
      {['C1', 'C3', 'C12'].map(competence => {
        const data = evaluations && evaluations[competence] ? evaluations[competence] : null;
        if (!data) {
          // Afficher une section vide si pas de donnes
          return (
            <div key={competence} style={{ marginBottom: '2mm' }}>
              <h3 style={{ 
                fontSize: '10pt', 
                fontWeight: 'bold', 
                margin: '0 0 1mm 0',
                color: '#1f2937',
                backgroundColor: '#e5e7eb',
                padding: '1mm',
                borderRadius: '1mm'
              }}>
                {competence} - {competenceDescriptions[competence]}
              </h3>
              <div style={{ 
                padding: '1mm', 
                textAlign: 'center', 
                color: '#6b7280',
                fontStyle: 'italic',
                backgroundColor: '#f9fafb',
                border: '1px dashed #d1d5db',
                borderRadius: '1mm',
                fontSize: '8pt'
              }}>
                Aucune valuation disponible pour cette comptence
              </div>
            </div>
          );
        }

        const competenceIndicators = indicators[competence] || [];
        const averageLevel = calculateAverageLevel(competence);

        return (
          <div key={competence} style={{ marginBottom: '2mm' }}>
            <h3 style={{ 
              fontSize: '10pt', 
              fontWeight: 'bold', 
              margin: '0 0 1mm 0',
              color: '#1f2937',
              backgroundColor: '#e5e7eb',
              padding: '1mm',
              borderRadius: '1mm'
            }}>
              {competence} - {competenceDescriptions[competence]}
            </h3>

            {/* Tableau des critres */}
            <table style={{ 
              width: '100%', 
              borderCollapse: 'collapse', 
              fontSize: '7pt',
              marginBottom: '1mm'
            }}>
              <thead>
                <tr style={{ backgroundColor: '#f3f4f6' }}>
                  <th style={{ 
                    border: '1px solid #d1d5db', 
                    padding: '0.5mm', 
                    textAlign: 'left',
                    width: '60%',
                    height: '4mm'
                  }}>
                    Critres d'valuation
                  </th>
                  <th style={{ 
                    border: '1px solid #d1d5db', 
                    padding: '0.3mm',
                    textAlign: 'center',
                    width: '10%',
                    height: '4mm'
                  }}>
                    valu
                  </th>
                  <th style={{ 
                    border: '1px solid #d1d5db', 
                    padding: '0.3mm',
                    textAlign: 'center',
                    width: '7.5%',
                    height: '4mm'
                  }}>
                    N1
                  </th>
                  <th style={{ 
                    border: '1px solid #d1d5db', 
                    padding: '0.3mm',
                    textAlign: 'center',
                    width: '7.5%',
                    height: '4mm'
                  }}>
                    N2
                  </th>
                  <th style={{ 
                    border: '1px solid #d1d5db', 
                    padding: '0.3mm',
                    textAlign: 'center',
                    width: '7.5%',
                    height: '4mm'
                  }}>
                    N3
                  </th>
                  <th style={{ 
                    border: '1px solid #d1d5db', 
                    padding: '0.3mm',
                    textAlign: 'center',
                    width: '7.5%',
                    height: '4mm'
                  }}>
                    N4
                  </th>
                </tr>
              </thead>
              <tbody>
                {/* Ligne de moyenne */}
                <tr style={{ backgroundColor: '#fef3c7', fontWeight: 'bold' }}>
                  <td style={{ 
                    border: '1px solid #d1d5db', 
                    padding: '0.5mm',
                    textAlign: 'center',
                    height: '4mm'
                  }}>
                    
                  </td>
                  <td style={{ 
                    border: '1px solid #d1d5db', 
                    padding: '0.3mm',
                    textAlign: 'center',
                    height: '4mm'
                  }}>
                    
                  </td>
                  {[1, 2, 3, 4].map(level => {
                    const isAverage = averageLevel === level;
                    return (
                      <td key={level} style={{ 
                        border: '1px solid #d1d5db', 
                        padding: '0.3mm',
                        textAlign: 'center',
                        backgroundColor: isAverage ? '#dbeafe' : 'transparent',
                        height: '4mm'
                      }}>
                        {isAverage ? (
                          <span style={{ 
                            color: '#dc2626', 
                            fontWeight: 'bold', 
                            fontSize: '10pt'
                          }}>
                            
                          </span>
                        ) : ''}
                      </td>
                    );
                  })}
                </tr>

                {/* Toujours afficher le mode dtaill avec tous les critres */}
                {competenceIndicators.map((indicator, index) => {
                  const criterion = data.criteria && data.criteria[indicator];
                  return (
                    <tr key={index}>
                      <td style={{ 
                        border: '1px solid #d1d5db', 
                          padding: '0.5mm',
                          fontSize: '7pt',
                          height: '4mm'
                      }}>
                        {indicator}
                      </td>
                      <td style={{ 
                        border: '1px solid #d1d5db', 
                          padding: '0.3mm',
                          textAlign: 'center',
                          height: '4mm'
                      }}>
                          {(criterion && criterion.evaluated) ? '' : ''}
                      </td>
                      {['N1', 'N2', 'N3', 'N4'].map((level, levelIndex) => {
                        const isSelected = criterion && criterion[level];
                          
                        return (
                          <td key={level} style={{ 
                            border: '1px solid #d1d5db', 
                              padding: '0.3mm',
                              textAlign: 'center',
                              height: '4mm'
                          }}>
                            {isSelected ? (
                              <span style={{ color: '#dc2626', fontWeight: 'bold', fontSize: '10pt' }}></span>
                            ) : ''}
                          </td>
                        );
                      })}
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        );
      })}

      {/* Note globale */}
      <div style={{ 
        marginTop: '2mm',
        padding: '1mm',
        textAlign: 'center',
        border: '1px solid #1e40af',
        borderRadius: '1mm',
        backgroundColor: '#eff6ff'
      }}>
        <div style={{ 
          fontSize: '12pt', 
          fontWeight: 'bold', 
          color: '#1e40af',
          marginBottom: '1mm'
        }}>
          Note globale : {(globalScore || 0).toFixed(2)}/20
        </div>
        {globalComments && (
          <div style={{ 
            fontSize: '8pt',
            fontStyle: 'italic',
            color: '#6b7280',
            marginTop: '1mm'
          }}>
            {globalComments}
          </div>
        )}
      </div>
    </div>
  );
};

export default EvaluationPrint;