import React, { useState, useEffect } from 'react';
import { X, Calendar, Clock, MapPin, User, Folder, FileText } from 'lucide-react';

const PlanningModal = ({ slot, students, projects, onSave, onClose }) => {
  const [formData, setFormData] = useState({
    student_id: '',
    project_id: '',
    phase: 1,
    phase_name: 'Planification',
    start_date: '',
    end_date: '',
    start_time: '',
    end_time: '',
    location: '',
    notes: '',
    status: 'scheduled',
    color: '#3B82F6'
  });

  const [errors, setErrors] = useState({});

  useEffect(() => {
    if (slot) {
      setFormData({
        student_id: slot.student_id || '',
        project_id: slot.project_id || '',
        phase: slot.phase || 1,
        phase_name: slot.phase_name || 'Planification',
        start_date: slot.start_date ? slot.start_date.split('T')[0] : '',
        end_date: slot.end_date ? slot.end_date.split('T')[0] : '',
        start_time: slot.start_time || '',
        end_time: slot.end_time || '',
        location: slot.location || '',
        notes: slot.notes || '',
        status: slot.status || 'scheduled',
        color: slot.color || '#3B82F6'
      });
    } else {
      // Dfauts pour un nouveau crneau
      const today = new Date();
      const todayStr = today.toISOString().split('T')[0];
      setFormData(prev => ({
        ...prev,
        start_date: todayStr,
        end_date: todayStr,
        start_time: '09:00',
        end_time: '10:00'
      }));
    }
  }, [slot]);

  const phases = [
    { value: 1, name: 'Planification', color: '#3B82F6' },
    { value: 2, name: 'Pilotage', color: '#F59E0B' },
    { value: 3, name: 'Soutenance', color: '#10B981' }
  ];

  const statusOptions = [
    { value: 'scheduled', label: 'Programm', color: 'text-blue-600' },
    { value: 'in_progress', label: 'En cours', color: 'text-orange-600' },
    { value: 'completed', label: 'Termin', color: 'text-green-600' },
    { value: 'cancelled', label: 'Annul', color: 'text-red-600' }
  ];

  const handleChange = (e) => {
    const { name, value } = e.target;
    
    if (name === 'phase') {
      const selectedPhase = phases.find(p => p.value === parseInt(value));
      setFormData(prev => ({
        ...prev,
        phase: parseInt(value),
        phase_name: selectedPhase?.name || '',
        color: selectedPhase?.color || '#3B82F6'
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value
      }));
    }
    
    // Effacer l'erreur pour ce champ
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};

    if (!formData.student_id) {
      newErrors.student_id = 'L\'tudiant est requis';
    }

    if (!formData.phase) {
      newErrors.phase = 'La phase est requise';
    }

    if (!formData.start_date) {
      newErrors.start_date = 'La date de dbut est requise';
    }

    if (!formData.end_date) {
      newErrors.end_date = 'La date de fin est requise';
    }

    if (formData.start_date && formData.end_date && formData.start_date > formData.end_date) {
      newErrors.end_date = 'La date de fin doit tre aprs la date de dbut';
    }

    if (formData.start_time && formData.end_time && formData.start_time >= formData.end_time) {
      newErrors.end_time = 'L\'heure de fin doit tre aprs l\'heure de dbut';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (validateForm()) {
      onSave(formData);
    }
  };

  const getStudentName = (student) => {
    return `${student.first_name} ${student.last_name} (${student.class})`;
  };

  return (
    <div className="fixed inset-0 z-50 overflow-y-auto">
      <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={onClose} />
        
        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-2xl sm:w-full">
          <form onSubmit={handleSubmit}>
            {/* En-tte */}
            <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-medium text-gray-900">
                  {slot ? 'Modifier le crneau' : 'Nouveau crneau'}
                </h3>
                <button
                  type="button"
                  onClick={onClose}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <X className="h-6 w-6" />
                </button>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {/* tudiant */}
                <div>
                  <label className="label">
                    <User className="h-4 w-4 mr-1" />
                    tudiant *
                  </label>
                  <select
                    name="student_id"
                    value={formData.student_id}
                    onChange={handleChange}
                    className={`input ${errors.student_id ? 'border-red-500' : ''}`}
                    required
                  >
                    <option value="">Slectionner un tudiant</option>
                    {students.map(student => (
                      <option key={student.id} value={student.id}>
                        {getStudentName(student)}
                      </option>
                    ))}
                  </select>
                  {errors.student_id && (
                    <p className="text-red-500 text-sm mt-1">{errors.student_id}</p>
                  )}
                </div>

                {/* Projet */}
                <div>
                  <label className="label">
                    <Folder className="h-4 w-4 mr-1" />
                    Projet
                  </label>
                  <select
                    name="project_id"
                    value={formData.project_id}
                    onChange={handleChange}
                    className="input"
                  >
                    <option value="">Aucun projet</option>
                    {projects.map(project => (
                      <option key={project.id} value={project.id}>
                        {project.title}
                      </option>
                    ))}
                  </select>
                </div>

                {/* Phase */}
                <div>
                  <label className="label">Phase *</label>
                  <select
                    name="phase"
                    value={formData.phase}
                    onChange={handleChange}
                    className={`input ${errors.phase ? 'border-red-500' : ''}`}
                    required
                  >
                    {phases.map(phase => (
                      <option key={phase.value} value={phase.value}>
                        Phase {phase.value} - {phase.name}
                      </option>
                    ))}
                  </select>
                  {errors.phase && (
                    <p className="text-red-500 text-sm mt-1">{errors.phase}</p>
                  )}
                </div>

                {/* Statut */}
                <div>
                  <label className="label">Statut</label>
                  <select
                    name="status"
                    value={formData.status}
                    onChange={handleChange}
                    className="input"
                  >
                    {statusOptions.map(status => (
                      <option key={status.value} value={status.value}>
                        {status.label}
                      </option>
                    ))}
                  </select>
                </div>

                {/* Date de dbut */}
                <div>
                  <label className="label">
                    <Calendar className="h-4 w-4 mr-1" />
                    Date de dbut *
                  </label>
                  <input
                    type="date"
                    name="start_date"
                    value={formData.start_date}
                    onChange={handleChange}
                    className={`input ${errors.start_date ? 'border-red-500' : ''}`}
                    required
                  />
                  {errors.start_date && (
                    <p className="text-red-500 text-sm mt-1">{errors.start_date}</p>
                  )}
                </div>

                {/* Date de fin */}
                <div>
                  <label className="label">
                    <Calendar className="h-4 w-4 mr-1" />
                    Date de fin *
                  </label>
                  <input
                    type="date"
                    name="end_date"
                    value={formData.end_date}
                    onChange={handleChange}
                    className={`input ${errors.end_date ? 'border-red-500' : ''}`}
                    required
                  />
                  {errors.end_date && (
                    <p className="text-red-500 text-sm mt-1">{errors.end_date}</p>
                  )}
                </div>

                {/* Heure de dbut */}
                <div>
                  <label className="label">
                    <Clock className="h-4 w-4 mr-1" />
                    Heure de dbut
                  </label>
                  <input
                    type="time"
                    name="start_time"
                    value={formData.start_time}
                    onChange={handleChange}
                    className="input"
                  />
                </div>

                {/* Heure de fin */}
                <div>
                  <label className="label">
                    <Clock className="h-4 w-4 mr-1" />
                    Heure de fin
                  </label>
                  <input
                    type="time"
                    name="end_time"
                    value={formData.end_time}
                    onChange={handleChange}
                    className={`input ${errors.end_time ? 'border-red-500' : ''}`}
                  />
                  {errors.end_time && (
                    <p className="text-red-500 text-sm mt-1">{errors.end_time}</p>
                  )}
                </div>

                {/* Lieu */}
                <div className="md:col-span-2">
                  <label className="label">
                    <MapPin className="h-4 w-4 mr-1" />
                    Lieu
                  </label>
                  <input
                    type="text"
                    name="location"
                    value={formData.location}
                    onChange={handleChange}
                    className="input"
                    placeholder="Salle, atelier, entreprise..."
                  />
                </div>

                {/* Notes */}
                <div className="md:col-span-2">
                  <label className="label">
                    <FileText className="h-4 w-4 mr-1" />
                    Notes
                  </label>
                  <textarea
                    name="notes"
                    value={formData.notes}
                    onChange={handleChange}
                    rows={3}
                    className="input"
                    placeholder="Notes additionnelles..."
                  />
                </div>
              </div>
            </div>

            {/* Actions */}
            <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
              <button
                type="submit"
                className="btn btn-primary sm:ml-3 sm:w-auto"
              >
                {slot ? 'Mettre  jour' : 'Crer'}
              </button>
              <button
                type="button"
                onClick={onClose}
                className="btn btn-secondary sm:mt-0 sm:w-auto"
              >
                Annuler
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default PlanningModal;