import React, { useMemo, useCallback } from 'react';
import { planningEngine } from './PlanningEngine';

// Composant de vue dtaille de la semaine
const DetailedWeekView = ({ 
  selectedWeek, 
  slots, 
  onSlotEdit, 
  onSlotDelete, 
  onSlotCreate, 
  onSlotResize, 
  onSlotMove, 
  onSlotCopy, 
  onSlotPaste, 
  copiedSlot, 
  currentViewState,
  onViewStateChange 
}) => {
  
  // Gnration des crneaux horaires
  const timeSlots = useMemo(() => {
    return [
      { time: '07:30', index: 0 },
      { time: '08:30', index: 1 },
      { time: '09:30', index: 2 },
      { time: '10:30', index: 3 },
      { time: '11:30', index: 4 },
      { time: '12:30', index: 5 },
      { time: '13:30', index: 6 },
      { time: '14:30', index: 7 },
      { time: '15:30', index: 8 },
      { time: '16:30', index: 9 },
      { time: '17:30', index: 10 }
    ];
  }, []);

  // Filtrage des crneaux pour la semaine slectionne
  const weekSlots = useMemo(() => {
    if (!selectedWeek || !slots) return [];
    
    return slots.filter(slot => {
      const slotDate = slot.start_date ? slot.start_date.split('T')[0] : null;
      return selectedWeek.some(day => {
        const dayDate = day.toISOString().split('T')[0];
        return slotDate === dayDate;
      });
    });
  }, [selectedWeek, slots]);

  // Gestionnaire de clic sur un crneau
  const handleSlotClick = useCallback((slot) => {
    if (onSlotEdit) {
      onSlotEdit(slot);
    }
  }, [onSlotEdit]);

  // Gestionnaire de suppression
  const handleSlotDelete = useCallback((slot) => {
    if (onSlotDelete) {
      onSlotDelete(slot);
    }
  }, [onSlotDelete]);

  // Gestionnaire de copie
  const handleSlotCopy = useCallback((slot) => {
    if (onSlotCopy) {
      onSlotCopy(slot);
    }
  }, [onSlotCopy]);

  // Gestionnaire de collage
  const handleSlotPaste = useCallback((day) => {
    if (onSlotPaste && copiedSlot) {
      onSlotPaste(copiedSlot, day);
    }
  }, [onSlotPaste, copiedSlot]);

  // Formatage de la date
  const formatDate = (date) => {
    if (!date || !(date instanceof Date) || isNaN(date.getTime())) {
      return 'Date invalide';
    }
    return date.toLocaleDateString('fr-FR', { 
      day: '2-digit', 
      month: '2-digit', 
      year: 'numeric' 
    });
  };

  // Formatage de l'affichage de la date
  const formatDisplayDate = (date) => {
    if (!date || !(date instanceof Date) || isNaN(date.getTime())) {
      return 'Date invalide';
    }
    return date.toLocaleDateString('fr-FR', { 
      weekday: 'long', 
      day: 'numeric', 
      month: 'long' 
    });
  };

  // Vrification si c'est un weekend
  const isWeekend = (date) => {
    if (!date || !(date instanceof Date) || isNaN(date.getTime())) {
      return false;
    }
    const day = date.getDay();
    return day === 0 || day === 6;
  };

  if (!selectedWeek) {
    return (
      <div className="flex items-center justify-center h-32 text-gray-500">
        Slectionnez une semaine pour voir les dtails
      </div>
    );
  }

  return (
    <div className="bg-white border border-gray-200 rounded-lg p-4">
      <div className="mb-4">
        <h3 className="text-lg font-medium text-gray-900">
          Dtails de la semaine
        </h3>
        <p className="text-sm text-gray-500">
          {formatDisplayDate(selectedWeek[0])} - {formatDisplayDate(selectedWeek[6])}
        </p>
      </div>

      {/* Grille horaire */}
      <div className="grid grid-cols-8 border border-gray-200 rounded-lg overflow-hidden">
        {/* En-tte des heures */}
        <div className="bg-gray-50 border-r">
          <div className="h-12 border-b flex items-center justify-center text-xs font-medium text-gray-700">
            Heures
          </div>
          {timeSlots.map((timeSlot) => (
            <div
              key={timeSlot.time}
              className="h-8 border-b border-gray-200 flex items-center justify-center text-xs text-gray-600 bg-gray-50"
            >
              {timeSlot.time}
            </div>
          ))}
        </div>

        {/* Colonnes des jours */}
        {selectedWeek.map((date, dayIndex) => {
          const dayInfo = formatDisplayDate(date);
          const daySlots = weekSlots.filter(slot => {
            const slotDate = slot.start_date ? slot.start_date.split('T')[0] : null;
            const cellDate = formatDate(date);
            return slotDate === cellDate;
          });

          return (
            <div key={dayIndex} className={`border-r ${dayIndex === 6 ? 'border-r-0' : ''}`}>
              {/* En-tte du jour */}
              <div className={`h-12 border-b flex flex-col items-center justify-center text-xs font-medium ${
                isWeekend(date) ? 'bg-gray-100 text-gray-600' : 'bg-white text-gray-900'
              }`}>
                <div className="font-bold">{dayInfo.split(' ')[0]}</div>
                <div>{date.getDate()}</div>
              </div>

              {/* Crneaux horaires */}
              {timeSlots.map((timeSlot, timeIndex) => {
                const slotAtTime = daySlots.find(slot => {
                  if (!slot.start_time) return false;
                  const slotStart = slot.start_time;
                  return slotStart === timeSlot.time;
                });

                return (
                  <div
                    key={timeIndex}
                    className={`h-8 border-b border-gray-200 relative ${
                      slotAtTime ? 'bg-blue-50' : 'bg-white'
                    }`}
                  >
                    {slotAtTime && (
                      <div
                        className="absolute inset-1 bg-blue-500 text-white text-xs p-1 rounded cursor-pointer hover:bg-blue-600 transition-colors"
                        onClick={() => handleSlotClick(slotAtTime)}
                        title={`${slotAtTime.student_name || 'tudiant'} - ${slotAtTime.start_time}  ${slotAtTime.end_time}`}
                      >
                        <div className="truncate font-medium">
                          {slotAtTime.student_name || 'tudiant'}
                        </div>
                        <div className="text-xs opacity-90">
                          {slotAtTime.start_time} - {slotAtTime.end_time}
                        </div>
                      </div>
                    )}
                  </div>
                );
              })}
            </div>
          );
        })}
      </div>

      {/* Actions */}
      <div className="mt-4 flex justify-between items-center">
        <div className="text-sm text-gray-500">
          {weekSlots.length} crneau{weekSlots.length > 1 ? 'x' : ''} cette semaine
        </div>
        
        <div className="flex space-x-2">
          {copiedSlot && (
            <button
              onClick={() => handleSlotPaste(selectedWeek[0])}
              className="px-3 py-1 text-xs bg-green-500 text-white rounded hover:bg-green-600 transition-colors"
            >
              Coller
            </button>
          )}
          
          <button
            onClick={() => onSlotCreate && onSlotCreate(selectedWeek[0])}
            className="px-3 py-1 text-xs bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
          >
            Nouveau crneau
          </button>
        </div>
      </div>
    </div>
  );
};

export default DetailedWeekView;
